// eslint-disable-next-line
import vertexShader from '!raw-loader!./sectionArea.vert';
// eslint-disable-next-line
import fragmentShader from '!raw-loader!./sectionArea.frag';

export const TEXTURE_UNIFORM: string = 'uTexture';
export const OVERLAY_COLOR_UNIFORM: string = 'uOverlayColor';

export const sectionAreaShader = () => ({
    uniforms: {
        [TEXTURE_UNIFORM]: { type: 't', value: undefined },
        [OVERLAY_COLOR_UNIFORM]: { value: [ 0, 0, 0, 0 ] },
    },
    vertexShader: vertexShader,
    fragmentShader: fragmentShader,
});
