import React, { useEffect, useRef, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { HeaderLogo } from './HeaderLogo';
import './Header.scss';

interface HeaderProps {
    scrollTop: number;
}

export function Header(props: HeaderProps) {
    const [shouldHideHeader, setShouldHideHeader] = useState(false);
    const shouldShowScrolled = props.scrollTop !== 0;
    const lastScrollTop = useRef(props.scrollTop);

    const scrolledStyle = shouldShowScrolled ? 'actions--scroll' : '';
    const hiddenStyle = shouldHideHeader ? 'hidden' : '';

    useEffect(() => {
        setShouldHideHeader(props.scrollTop - lastScrollTop.current > 0);
        lastScrollTop.current = props.scrollTop;
    }, [props.scrollTop, lastScrollTop, setShouldHideHeader])

    return (
        <header className="Header">
            <HeaderLogo />
            <nav className={`actions ${scrolledStyle} ${hiddenStyle}`} >
                <NavLink exact to="/">Strona Gwna</NavLink>
                <NavLink exact to="/planes">Samoloty</NavLink>
                <NavLink exact to="/team">Zesp</NavLink>
                <NavLink exact to="/contact">Kontakt</NavLink>
            </nav>
        </header>
    );
}
