import React, { useCallback, useContext, useEffect, useMemo, useRef, useState } from 'react';
import { ListenerRemover, SectionEventListener } from '../../3d/context/sectionBase';
import { SectionController } from '../../3d/context/sectionController';
import { PageContext } from './Page';
import { ThreeContextContext } from './ThreeContextProvider';

export interface SectionProps {
    children: React.ReactNode | React.ReactNode[] | ((context: SectionContext) => React.ReactNode[]);
    sectionKey: string;
    makeController: () => SectionController<any, any>;
}

export interface SectionContext {
    addListener(event: string, listener: SectionEventListener): ListenerRemover;
}

export function Section({ children, sectionKey, makeController }: SectionProps) {
    const threeContext = useContext(ThreeContextContext);
    const pageContext = useContext(PageContext);
    const pageKey = useMemo(() => pageContext?.pageKey || null, [ pageContext ]);
    const [ sectionRegistered, setSectionRegistered ] = useState(false);

    const elementRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        pageContext?.registerSection(sectionKey, makeController());
        setSectionRegistered(true);
    }, [/* Keeping the list empty so that this is called only once. */]);

    useEffect(() => {
        const handler = () => {
            const rect = elementRef.current?.getBoundingClientRect();

            if (threeContext && pageKey && sectionKey && rect) {
                threeContext.setSectionHeight(pageKey, sectionKey, rect.height);
            }
        };

        handler();

        window.addEventListener('resize', handler, false);

        return () => window.removeEventListener('resize', handler, false);
    }, [ threeContext, pageKey, sectionKey, elementRef ]);

    const addListener = useCallback((event: string, listener: SectionEventListener) => {
        if (pageKey) {
            return threeContext?.addListener(pageKey, sectionKey, event, listener) || (() => {});
        }
        else {
            console.error(`Couldn't register a listener without a Page to attach to: '${pageKey}'`);
        }

        return (() => {});
    }, [ pageKey, threeContext, sectionKey, sectionRegistered ]);

    return (
        <div ref={elementRef}>
            {children instanceof Function ? children({ addListener }) : children}
        </div>
    );
}