import { createContext } from 'react';

export interface ScrollContext {
    /**
     * How much content is hidden at the top of the container.
     */
    scrollTop: number;
    /**
     * The height of the scroll container.
     */
    viewHeight: number;
}

const defaultScrollContext: ScrollContext = {
    scrollTop: 0,
    viewHeight: 1,
};

export const ScrollContext = createContext(defaultScrollContext);