import { Color, WebGLRenderer } from 'three';

export enum RendererParams {
    AUTO_CLEAR = 'autoClear',
    CLEAR_COLOR = 'clearColor',
    CLEAR_ALPHA = 'clearAlpha',
}

type RendererParamTypes = {
    [RendererParams.AUTO_CLEAR]: boolean,
    [RendererParams.CLEAR_COLOR]: Color,
    [RendererParams.CLEAR_ALPHA]: number,
};

interface StoreAction<P extends RendererParams> {
    (renderer: WebGLRenderer, overrideValue: RendererParamTypes[P]): () => void,
}

const paramStoreActions: {[K in keyof RendererParamTypes]: StoreAction<K>} = {
    autoClear(renderer: WebGLRenderer, overrideValue: boolean) {
        // Store old value
        const oldAutoClear = renderer.autoClear;
        renderer.autoClear = overrideValue;

        // Restore old value in a callback
        return () => renderer.autoClear = oldAutoClear;
    },

    clearColor(renderer: WebGLRenderer, overrideValue: Color) {
        // Store old value
        const oldClearColor = renderer.getClearColor().getHex();
        renderer.setClearColor(overrideValue);

        // Restore old value in a callback
        return () => renderer.setClearColor(oldClearColor, renderer.getClearAlpha());
    },

    clearAlpha(renderer: WebGLRenderer, overrideValue: number) {
        // Store old value
        const oldClearAlpha = renderer.getClearAlpha();
        renderer.setClearAlpha(overrideValue);

        // Restore old value in a callback
        return () => renderer.setClearAlpha(oldClearAlpha);
    },
};

export function withParams(renderer: WebGLRenderer, params: Partial<RendererParamTypes>, action: () => void) {
    let restorers: (() => void)[] = [];

    for (const p of Object.values(RendererParams)) {
        if (params[p] !== undefined) {
            const restorer = paramStoreActions[p as RendererParams](renderer, params[p] as never);

            restorers.splice(0, 0, restorer);
        }
    }

    // Perform action
    action();

    // Restore old values
    restorers.forEach(r => r());
}
