import { Vector3, OrthographicCamera } from 'three';

export class BackgroundCamera extends OrthographicCamera {
    private smoothOffset: Vector3 = new Vector3();
    private targetOffset: Vector3 = new Vector3();
    private snapSpeed: number = 1;

    constructor(private origin: Vector3, left: number, right: number, top: number, bottom: number, near: number, far: number) {
        super(left, right, top, bottom, near, far);

        this.position.set(origin.x, origin.y, origin.z);
    }

    public setOffset(x: number, y: number, z: number, snapSpeed: number): void {
        this.targetOffset.set(x, y, z);
        this.snapSpeed = snapSpeed;
    }

    public update(): void {
        // Updating the camera position.
        this.smoothOffset.set(
            (this.targetOffset.x - this.smoothOffset.x) * this.snapSpeed,
            (this.targetOffset.y - this.smoothOffset.y) * this.snapSpeed,
            (this.targetOffset.z - this.smoothOffset.z) * this.snapSpeed,
        );

        this.position.set(this.origin.x + this.smoothOffset.x, this.origin.y + this.smoothOffset.y, this.origin.z + this.smoothOffset.z);
    }
}