import { BannerSectionConfig } from './banner/bannerSection';
import { ThreeContext } from './context/threeContext';
import { PlaneSectionConfig } from './plane/planeSection';
import { SliderSectionConfig } from './slider/sliderSection';
import { TimelineSectionConfig } from './timeline/timelineSection';

export enum HomeSectionKey {
    BANNER_SECTION = 'bannerSection',
    PLANE_SECTION = 'planeSection',
    TIMELINE_SECTION = 'timelineSection',
    SLIDER_SECTION = 'sliderSection',
};

export enum SimpleSectionKey {
    MAIN_SECTION = 'mainSection',
};

export enum PageKey {
    HOME = '/',
    PLANES = '/planes',
    TEAM = '/team',
    CONTACT = '/contact',
}

export interface HomePageConfig {
    [HomeSectionKey.BANNER_SECTION]?: BannerSectionConfig;
    [HomeSectionKey.PLANE_SECTION]?: PlaneSectionConfig;
    [HomeSectionKey.TIMELINE_SECTION]?: TimelineSectionConfig;
    [HomeSectionKey.SLIDER_SECTION]?: SliderSectionConfig;
}

export interface SimplePageConfig {
    [SimpleSectionKey.MAIN_SECTION]?: {};
}

export type SolarPageConfigMap = {
    [PageKey.HOME]: HomePageConfig,
    [PageKey.PLANES]: SimplePageConfig,
    [PageKey.TEAM]: SimplePageConfig,
    [PageKey.CONTACT]: SimplePageConfig,
}

export const solarContext = new ThreeContext<SolarPageConfigMap>();