import { ShaderMaterial, Texture, WebGLRenderer, WebGLRenderTarget } from 'three';
import { Pass } from 'three/examples/jsm/postprocessing/Pass';
import { pageBackgroundShader, Uniforms } from './pageBackgroundShader';

export interface PageBackgroundSpecification {
    primaryIndex: number;
    secondaryIndex: number;
    mix: number;
}

export class PageBackgroundPass extends Pass {

    private material: ShaderMaterial;
    private fsQuad: Pass.FullScreenQuad;

    constructor(gradientSheetMap: Texture, frameHeight: number) {
        super();
    
        this.material = new ShaderMaterial({
            ...pageBackgroundShader(),
            depthTest: false,
            depthWrite: false
        });

        this.material.uniforms[Uniforms.GRADIENT_SHEET_UNIFORM].value = gradientSheetMap;
        this.material.uniforms[Uniforms.FRAME_HEIGHT].value = frameHeight;
        this.material.uniforms[Uniforms.FRAME_HEIGHT].value = frameHeight;

        this.clear = true;
        this.needsSwap = false;
        this.fsQuad = new Pass.FullScreenQuad(this.material);
    }

    setSpecification(spec: PageBackgroundSpecification): void {
        const uniforms = this.material.uniforms;
        uniforms[Uniforms.PRIMARY_INDEX].value = spec.primaryIndex;
        uniforms[Uniforms.SECONDARY_INDEX].value = spec.secondaryIndex;
        uniforms[Uniforms.FRAME_MIX_UNIFORM].value = spec.mix;
        uniforms[Uniforms.PAGE_TRANSITION_MIX_UNIFORM].value = 0;
    }

    setTransitionSpecification(specFrom: PageBackgroundSpecification, specTo: PageBackgroundSpecification, progress: number): void {
        const uniforms = this.material.uniforms;
        uniforms[Uniforms.PRIMARY_INDEX].value = specFrom.primaryIndex;
        uniforms[Uniforms.SECONDARY_INDEX].value = specFrom.secondaryIndex;
        uniforms[Uniforms.FRAME_MIX_UNIFORM].value = specFrom.mix;
        uniforms[Uniforms.PRIMARY_TARGET_INDEX].value = specTo.primaryIndex;
        uniforms[Uniforms.SECONDARY_TARGET_INDEX].value = specTo.secondaryIndex;
        uniforms[Uniforms.FRAME_TARGET_MIX_UNIFORM].value = specTo.mix;
        uniforms[Uniforms.PAGE_TRANSITION_MIX_UNIFORM].value = progress;
    }

    public render(renderer: WebGLRenderer, writeBuffer: WebGLRenderTarget, readBuffer: WebGLRenderTarget /*, deltaTime, maskActive */ ) {
		const oldAutoClear = renderer.autoClear;
		renderer.autoClear = false;

        renderer.setRenderTarget(this.renderToScreen ? null : readBuffer);
        
        if (this.clear)
            renderer.clear();

		this.fsQuad.material = this.material;
		this.fsQuad.render(renderer);

		renderer.autoClear = oldAutoClear;
	}
}