export type SectionEventListener = (value: any) => void;
export type ListenerRemover = () => void;

export abstract class SectionBase {
    private _sectionHeight: number = 0;
    protected progress: number = 0;
    protected eventListeners: {[key: string]: SectionEventListener[]} = {};

    public backgroundSpec = {
        primaryIndex: 1,
        secondaryIndex: 2,
        mix: 0,
    };

    get sectionHeight() {
        return this._sectionHeight;
    }

    setSectionHeight(value: number) {
        this._sectionHeight = value;
    }

    setSectionProgress(progress: number) {
        this.progress = progress;
    }

    notifyEvent(event: string, value: any): void {
        if (this.eventListeners[event]) {
            this.eventListeners[event].forEach(l => l(value));
        }
    }

    addListener(event: string, listener: (value: any) => void): ListenerRemover {
        if (!this.eventListeners[event]) {
            this.eventListeners[event] = [];
        }

        this.eventListeners[event].push(listener);

        // Adding a remover.
        return () => this.eventListeners[event] = this.eventListeners[event].filter(l => l !== listener);
    }
}
