import { ThreeContextConfig } from './threeContext';
import { Easing, easingFunctions } from '../../utils/easing';
import { lerp } from '../../utils/math';
import { PageTransition, TransitionContext } from './transition';
import { ThreePage } from './threePage';

export function createFadeTransition<C extends ThreeContextConfig<C>>(duration: number): PageTransition<C> {
    const startTime = Date.now();

    return {
        duration: duration,

        perform(context: TransitionContext<C>, prevPage: ThreePage<C>, currentPage: ThreePage<C>): boolean {
            const now = Date.now();
            const progress = (now - startTime) / duration;

            if (progress >= 1) {
                return true;
            }

            const current = prevPage.currentBreakpoint;
            const nextPageBreakpoint = currentPage.currentBreakpoint;

            if (current === null || nextPageBreakpoint === null) {
                return true;
            }

            const next = prevPage.nextBreakpoint;

            if (next !== null) {
                const originalPosition = (next.progress - prevPage.progress);
                const newPosition = window.innerHeight;
                const t = easingFunctions[Easing.EASE_IN_OUT_SINE](progress);
                context.overlapPosition = lerp(originalPosition, newPosition, t);
            }

            context.primarySectionDisplay.setSceneOpacity(Math.abs(1 - progress * 2));
            context.primarySectionDisplay.setTransition(current.section.backgroundSpec, nextPageBreakpoint.section.backgroundSpec, easingFunctions[Easing.EASE_IN_OUT_QUAD](progress));

            context.showSections(
                progress < 0.5 ? current.section : nextPageBreakpoint.section,
                next?.section
            );

            return false;
        }
    };
}