import { Group, Material, Mesh, FontLoader, Font } from 'three';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';

export interface ILoadModelOptions {
    materials: { [key: string]: Material | Material[] }
}

const initialModelOptions: ILoadModelOptions = {
    materials: {},
};

export function createModelLoader() {
    const loader = new OBJLoader();

    return {
        load(url: string, opt: ILoadModelOptions = initialModelOptions): Promise<Group> {
            return new Promise((resolve, reject) => {
                loader.load(url, (scene) => {
                    for (const key of Object.keys(opt.materials)) {
                        (scene.getObjectByName(key) as Mesh).material = opt.materials[key];
                    }

                    resolve(scene);
                }, undefined, (error) => {
                    reject(error);
                });
            });
        }
    };
}

export function getFont(json: Object): Promise<Font> {
    let loader = new FontLoader();
    return new Promise((resolve, reject) => {
        resolve(loader.parse(json));
    });
}