import { Camera, Color, Material, NearestFilter, Scene, ShaderMaterial, UniformsUtils, WebGLRenderer, WebGLRenderTarget } from 'three';
import { Pass } from 'three/examples/jsm/postprocessing/Pass';
import { CopyShader } from 'three/examples/jsm/shaders/CopyShader';
import { withParams } from '../../../utils/effectUtils';
import { mixShader, Uniforms } from './mixShader/mixShader';

export class RenderWithOpacityPass extends Pass {
    public clearDepth: boolean;

    private copyMaterial: ShaderMaterial;
    private mixMaterial: ShaderMaterial;
    private renderTargetScene: WebGLRenderTarget;
    private fsQuad: Pass.FullScreenQuad;

    constructor(public scene: Scene, public camera: Camera, public opacity: number = 1, private overrideMaterial?: Material, private clearColor?: Color, private clearAlpha: number = 0) {
        super();

        this.renderTargetScene = new WebGLRenderTarget(1, 1, {
            minFilter: NearestFilter,
            magFilter: NearestFilter
        });

        this.copyMaterial = new ShaderMaterial({
            uniforms: UniformsUtils.clone(CopyShader.uniforms),
            vertexShader: CopyShader.vertexShader,
            fragmentShader: CopyShader.fragmentShader,
            depthTest: false,
            depthWrite: false,
        });

        this.mixMaterial = new ShaderMaterial({
            ...mixShader(),
            depthTest: false,
            depthWrite: false,
        });

        this.fsQuad = new Pass.FullScreenQuad();

        this.clear = true;
        this.clearDepth = false;
        this.needsSwap = true;
    }

    setSize(width: number, height: number) {
        this.renderTargetScene.setSize(width, height);
    }

    render(renderer: WebGLRenderer, writeBuffer: WebGLRenderTarget, readBuffer: WebGLRenderTarget, deltaTime: number, maskActive: boolean) {
        let oldOverrideMaterial;

        if (this.overrideMaterial !== undefined) {
            oldOverrideMaterial = this.scene.overrideMaterial;
            this.scene.overrideMaterial = this.overrideMaterial;
        }

        withParams(renderer, {
            autoClear: false,
            clearColor: new Color(0, 0, 0),
        }, () => {
            // Copying the background
            renderer.setRenderTarget(this.renderTargetScene);
            renderer.clear();

		    this.copyMaterial.uniforms['tDiffuse'].value = readBuffer.texture;
            
            this.fsQuad.material = this.copyMaterial;
            this.fsQuad.render(renderer);
            
            // Rendering the scene
            renderer.render(this.scene, this.camera);
            
            // Compositing with opacity
            renderer.setRenderTarget(this.renderToScreen ? null : writeBuffer);
            
            this.mixMaterial.uniforms[Uniforms.MIX].value = this.opacity;
            this.mixMaterial.uniforms[Uniforms.PRIMARY_TEXTURE].value = readBuffer.texture;
            this.mixMaterial.uniforms[Uniforms.SECONDARY_TEXTURE].value = this.renderTargetScene.texture;

            if (this.clearColor)
                renderer.setClearColor(this.clearColor);

            // TODO: Avoid using autoClear properties, see https://github.com/mrdoob/three.js/pull/15571#issuecomment-465669600
            if (this.clear)
                renderer.clear(renderer.autoClearColor, renderer.autoClearDepth, renderer.autoClearStencil);

            this.fsQuad.material = this.mixMaterial;
            this.fsQuad.render(renderer);
        });

        if (oldOverrideMaterial !== undefined) {
            this.scene.overrideMaterial = oldOverrideMaterial;
        }
    }
}