import * as THREE from "three";
import { Easing, easingFunctions } from '../../../utils/easing';

export default class TouchTexture {
    parent: any;
    size: number;
    maxAge: number;
    radius: number;
    trail: any[];
    canvas!: HTMLCanvasElement;
    ctx: any;
    texture: any;
    lastTouchTime = Date.now();

    constructor(parent: any) {
        this.parent = parent;
        this.size = 64;
        this.maxAge = 120;
        this.radius = 0.15;
        this.trail = [];

        this.initTexture();
    }

    initTexture() {
        this.canvas = document.createElement('canvas');
        this.canvas.width = this.canvas.height = this.size;
        this.ctx = this.canvas.getContext('2d');
        this.ctx.fillStyle = 'rgba(0,0,0,1)';
        this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);

        this.texture = new THREE.Texture(this.canvas);

        this.canvas.id = 'touchTexture';
        this.canvas.style.width = this.canvas.style.height = `${this.canvas.width}px`;
        this.canvas.style.position = "absolute";
        this.canvas.style.left = "0";
        this.canvas.style.top = "0";
        this.canvas.style.display = "none";
        document.body.appendChild(this.canvas);
    }

    update(delta: number) {
        this.clear();
        this.ctx.globalCompositeOperation = "lighter";
        this.texture.needsUpdate = true;
        // age points
        this.trail.forEach((point, i) => {
            point.age++;
            // remove old
            if (point.age > this.maxAge) {
                this.trail.splice(i, 1);
            }
        });

        this.trail.forEach((point, i) => {
            this.drawTouch(point);
        });

        this.texture.needsUpdate = true;
    }

    clear() {
        this.ctx.globalCompositeOperation = "source-over";
        this.ctx.fillStyle = 'black';
        this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
    }

    addTouch(point: { x: number; y: number; }) {
        let now = Date.now();
        if (now - this.lastTouchTime < 100) {
            return
        }
        this.lastTouchTime = now;

        let force = 0;
        const last = this.trail[this.trail.length - 1];
        if (last) {
            const dx = last.x - point.x;
            const dy = last.y - point.y;
            const dd = dx * dx + dy * dy;
            force = Math.min(dd * 10000, 1);
        }
        this.trail.push({ x: point.x, y: point.y, age: 0, force });
    }

    drawTouch(point: any) {
        const pos = {
            x: point.x * this.size,
            y: point.y * this.size
        };

        let intensity = 1;
        if (point.age < this.maxAge * 0.3) {
            intensity = easingFunctions[Easing.EASE_OUT_CUBIC](point.age / (this.maxAge * 0.3));
        } else {
            intensity = easingFunctions[Easing.EASE_OUT_CUBIC](1 - (point.age - this.maxAge * 0.3) / (this.maxAge * 0.7));
        }

        intensity *= point.force;

        const radius = this.size * this.radius * intensity;
        const grd = this.ctx.createRadialGradient(pos.x, pos.y, radius * 0.25, pos.x, pos.y, radius);
        grd.addColorStop(0, `rgba(0, 255, 0, 0.2)`);
        grd.addColorStop(1, 'rgba(0, 0, 0, 0.0)');

        this.ctx.beginPath();
        this.ctx.fillStyle = grd;
        this.ctx.arc(pos.x, pos.y, radius, 0, Math.PI * 2);
        this.ctx.fill();
    }

}
