import { Camera, Clock, Group, Object3D, Vector3 } from 'three';
import PathBuilder, { createPathBuilder, PathElementData, PathNode } from './pathBuilder';

export interface TimelineConfig {
}

/**
 * Uses the sigmoid function around the t=0 point.
 * Returns:
 *   0 for t -> -inf
 *   1 for t -> +inf
 *   and ease-in-out curve for t values around -radius and radius
 * @param t 
 * @param radius 
 */
function smoothTransition(t: number, radius: number): number {
    if (t < 0) return 0;
    const trans = Math.min(Math.abs(t / radius), 1);
    // const scaledProgress = 1 / (1 + Math.exp(-t * 4 / radius));
    // return scaledProgress * (1 - trans) + Math.sign(t) * trans;
    return Math.sin((trans * Math.PI) / 2)
}



export class Timeline extends Object3D {
    private pathBuilder?: PathBuilder;
    private pathGroup: Group = new Group();
    private clock: Clock;
    private progress: number = 1;
    private maxScroll = 0;
    private downPosition: number = -60;
    private data: PathNode[] = [];
    private scrollVector: Vector3 = new Vector3(1, 0, 1).normalize();
    private maxX = 0;
    private notify: (event: string, value: any) => void;

    constructor(pathData: PathElementData[], notify: (event: string, value: any) => void) {
        super();
        this.notify = notify;
        (async () => {
            this.pathBuilder = await createPathBuilder();
            this.data = this.pathBuilder.createPathObjects(pathData).map((item) => {
                if (!item.anchored) {
                    item.obj.position.y = this.downPosition;
                }
                this.pathGroup.add(item.obj);

                const itemPosition = this.scrollVector.dot(item.obj.position);
                if (this.maxScroll < itemPosition) {
                    this.maxScroll = itemPosition;
                }

                if (this.maxX < item.obj.position.x) {
                    this.maxX = item.obj.position.x;
                }

                return item;
            });

            // this.maxScroll += 10;
        })();
        this.clock = new Clock();
        this.add(this.pathGroup);
    }

    setConfig(config: TimelineConfig) {
    }

    setProgress(progress: number) {
        this.progress = progress;
    }

    onUpdate(camera: Camera) {
        const animationOffset = 2//20;
        // How fast the blocks move into place.
        const animationDuration = 2;

        // this.pathGroup.position.set(
        //     -this.scrollVector.x * this.progress * this.maxScroll,
        //     0,
        //     -this.scrollVector.z * this.progress * this.maxScroll,
        // );


        var delta = this.clock.getDelta();
        var active = 1;
        this.data.forEach((item, index) => {
            if (item.anchored)
                return;

            const itemPosition = index / this.data.length //this.scrollVector.dot(item.obj.position);
            let scaledProgress = this.progress * this.maxScroll + animationOffset - itemPosition * this.maxScroll;

            item.obj.position.y = smoothTransition(scaledProgress, animationDuration) * -this.downPosition + this.downPosition;
            if (item.obj.position.y === 0) {
                active = index;
            }
        });
        var xp = -0.5;
        var zp = 2;

        if (this.data[active]) {
            var vec = new Vector3(this.data[active]?.obj.position.x + xp, this.data[active]?.obj.position.y, this.data[active]?.obj.position.z + zp);
            // vec.x+=this.scrollVector.x * this.progress * this.maxScroll;
            // vec.z+=this.scrollVector.z * this.progress * this.maxScroll;

            vec.x = (500 + vec.x / 10 - camera.position.x) * 3 * delta;
            vec.z = (500 + vec.z / 10 - camera.position.z) * 3 * delta;

            camera.position.x += vec.x;
            camera.position.z += vec.z;
        }


        if (this.pathBuilder) {
            this.pathBuilder.update();
        }
    }
}
