import { BoxGeometry, Mesh, Object3D, ShaderMaterial } from 'three';
import { planeShader } from './planeShader';

export const airflowRodMaterial = new ShaderMaterial({
    ...planeShader([ 1, 1, 1]),
});

export class AirflowRod extends Object3D {
    private model: Mesh;

    constructor(
        private offset: number,
    ) {
        super();

        this.model = new Mesh(new BoxGeometry(0.002, 0.002, 0.05), airflowRodMaterial);
        this.add(this.model);
    }

    onUpdate() {
        if (this.model !== null) {
            const now = Date.now();
            const duration = 500;
            const t = (now + this.offset) % duration / duration;
            const x = 0.15 - t * 0.3;
            const scale = 0.5 - Math.cos(t * Math.PI * 2) * 0.5;

            this.model.position.set(0, 0, x);
            this.model.scale.set(scale, scale, 1);
        }
    }
}