// eslint-disable-next-line
import vertexShader from '!raw-loader!./mixShader.vert';
// eslint-disable-next-line
import fragmentShader from '!raw-loader!./mixShader.frag';

export enum Uniforms {
    MIX = 'uMix',
    PRIMARY_TEXTURE = 'uPrimaryTexture',
    SECONDARY_TEXTURE = 'uSecondaryTexture',
}

export const mixShader = () => ({
    uniforms: {
        [Uniforms.MIX]: { value: 0 },
        [Uniforms.PRIMARY_TEXTURE]: { type: 't', value: undefined },
        [Uniforms.SECONDARY_TEXTURE]: { type: 't', value: undefined },
    },
    vertexShader: vertexShader,
    fragmentShader: fragmentShader,
});
