// eslint-disable-next-line
import vertexShader from '!raw-loader!./flag.vert';
// eslint-disable-next-line
import fragmentShader from '!raw-loader!./flag.frag';
import { Color, Texture } from 'three';

export const COLOR_UNIFORM: string = 'uColor';
export const WAVE_MASK_TEXTURE_UNIFORM: string = 'uWaveMaskTexture';
export const TIME_UNIFORM: string = 'uTime';

export const flagShader = (color: number, waveMask: Texture | null) => ({
    uniforms: {
        [COLOR_UNIFORM]: { value: new Color(color) },
        [WAVE_MASK_TEXTURE_UNIFORM]: { type: 't', value: waveMask || undefined },
        [TIME_UNIFORM]: { value: 0 },
    },
    vertexShader: vertexShader,
    fragmentShader: fragmentShader,
    transparent: true,
});
