import { RefObject, useContext, useEffect, useMemo, useState } from 'react';
import { ScrollContext } from '../abstract/ScrollContext';

/**
 * Returns true if the ref element is deemed visible.
 * @param ref 
 * @param distanceFromBottom Minimum distance from the bottom required for this element to be recognised as 'visible'.
 * @returns 
 */
export function useIsVisible(ref: RefObject<HTMLElement>, distanceFromBottom?: number): boolean {
    const { scrollTop, viewHeight } = useContext(ScrollContext);

    const visibility = useMemo(() => {
        const rect = ref.current?.getBoundingClientRect();

        if (rect) {
            return rect.top <= viewHeight - (distanceFromBottom || 0);
        }

        return false;
    }, [ ref, scrollTop, viewHeight, distanceFromBottom ]);

    return visibility;
}

/**
 * Returns true if the ref element is visible or was visible at one point.
 * @param ref 
 * @param distanceFromBottom Minimum distance from the bottom required for this element to be recognised as 'visible'.
 */
export function useHasAppeared(ref: RefObject<HTMLElement>, distanceFromBottom?: number): boolean {
    // Is currently visible.
    const isVisible = useIsVisible(ref, distanceFromBottom);

    // Was visible at least once.
    const [wasVisible, setWasVisible] = useState(false);

    useEffect(() => {
        if (isVisible && !wasVisible) {
            setWasVisible(true);
        }
    }, [ isVisible, wasVisible, setWasVisible ]);

    return wasVisible;
}