import { AmbientLight, Color, DirectionalLight, OrthographicCamera, Scene, Vector3 } from 'three';
import { SectionController } from '../context/sectionController';
import { HomeSectionKey, HomePageConfig } from '../solarContext';
import { Timeline, TimelineConfig } from './timeline';
import { CSS3DRenderer } from 'three/examples/jsm/renderers/CSS3DRenderer.js';
import PATH_DATA from '../../data/timeline';
import { SectionBase } from '../context/sectionBase';

export interface TimelineSectionConfig {
    timeline?: TimelineConfig;
}

export class TimelineSection extends SectionBase implements SectionController<HomePageConfig, typeof HomeSectionKey.TIMELINE_SECTION> {
    public scene: Scene;
    public camera: OrthographicCamera;
    public timeline: Timeline;

    private rendererCSS: CSS3DRenderer;

    public backgroundColor: Color = new Color(0x334488);
    public backgroundSpec = {
        primaryIndex: 6,
        secondaryIndex: 5,
        mix: 0,
    };

    private innerProgress: number = 0;

    constructor() {
        super();
        this.rendererCSS = new CSS3DRenderer();
        this.rendererCSS.setSize(window.innerWidth, window.innerHeight);
        this.rendererCSS.domElement.style.position = 'absolute';
        this.rendererCSS.domElement.style.top = '0px';
        this.rendererCSS.domElement.style.pointerEvents = "none";
        document.body.appendChild(this.rendererCSS.domElement);

        this.scene = new Scene();
        this.scene.background = null;
        // this.scene.background = this.backgroundColor;
        this.camera = new OrthographicCamera(0, 1, 1, 0, -10000, 10000);
        this.camera.position.set(500, 400, 500);
        this.camera.lookAt(0, 0, 0);

        var ambientLight = new AmbientLight(0x22ffff, 0.2);
        this.scene.add(ambientLight);

        var directionalLight = new DirectionalLight(0xffffff, 1.2);
        directionalLight.position.set(100, 150, 60);
        directionalLight.lookAt(0, 0, 0);
        this.scene.add(directionalLight);

        this.timeline = new Timeline(PATH_DATA, this.notifyEvent.bind(this));
        this.timeline.position.set(0.45, 0, 0);
        this.timeline.scale.set(0.1, 0.1, 0.1);
        this.scene.add(this.timeline);
        console.log(this)
    }

    setConfig(config: TimelineSectionConfig) {
        this.timeline.setConfig(config.timeline || {});
    }

    public updateCameraProjection(width: number, height: number) {
        const scale = width/1000;
        const ratio = height / width;
        this.camera.left = 0;
        this.camera.right = scale;
        this.camera.bottom = -ratio / 2 * scale;
        this.camera.top = ratio / 2 * scale;
        this.camera.updateProjectionMatrix();
        this.rendererCSS.setSize(width, height);
    }

    onUpdate(delta: number, elapsedTime: number) {
        this.timeline.setProgress(this.innerProgress / (this.sectionHeight - window.innerHeight));
        this.timeline.onUpdate(this.camera);
        this.rendererCSS.render(this.scene, this.camera);
    }

    setSectionProgress(progress: number) {
        super.setSectionProgress(progress);
        this.innerProgress = progress;
        this.backgroundSpec.mix = progress / this.sectionHeight;

        // this.notifyEvent('progress-changed', this.innerProgress);
    }

}
