import * as THREE from 'three';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { getFont } from '../../utils/loaders';
import pathSolar from '../../assets/models/path-solar.obj';
import flagWaveMask from '../../assets/img/flag-wave-mask.png';
import Roboto_Regular from '../../assets/fonts/Roboto_Regular.json';
import { CSS3DObject } from 'three/examples/jsm/renderers/CSS3DRenderer.js';
import { flagShader, WAVE_MASK_TEXTURE_UNIFORM, TIME_UNIFORM } from './flagShader';
import { ClampToEdgeWrapping, Texture, TextureLoader, Wrapping } from 'three';

export type PathElementType = 'straight' | 'turnLeft' | 'turnRight' | 'tip';


export interface PathNode {
    anchored: boolean;
    obj: THREE.Object3D;
}

interface PathElementTypeMetadata {
    templateName: string;
    modelRotation: number;
    rotationOffset: number;
    forwardOffset: number;
    sideOffset: number;
}

const TYPE_METADATA: {[key in PathElementType]: PathElementTypeMetadata} = {
    'straight': {
        templateName: 'straight',
        modelRotation: -1,
        rotationOffset: 0,
        forwardOffset: 1,
        sideOffset: 0,
    },
    'turnLeft': {
        templateName: 'turn',
        modelRotation: 2,
        rotationOffset: -1,
        forwardOffset: 0,
        sideOffset: -1,
    },
    'turnRight': {
        templateName: 'turn',
        modelRotation: 1,
        rotationOffset: 1,
        forwardOffset: 0,
        sideOffset: 1,
    },
    'tip': {
        templateName: 'tip',
        modelRotation: 1,
        rotationOffset: 0,
        forwardOffset: 1,
        sideOffset: 0,
    },
};

export interface PathElementData {
    type: PathElementType;
    color: number;
    flag?: boolean;
    date?: string;
    header?: string;
    description?: string;
    textShift?: number;
}

function getModel(path: string) {
    let loader = new OBJLoader();
    let material = new THREE.MeshPhysicalMaterial({ color: 0x555555 });

    return new Promise<THREE.Group>((resolve, reject) => {
        loader.load(path, (object: THREE.Group) => {
            object.traverse(function (child) {
                if (child instanceof THREE.Mesh) {
                    child.material = material;
                }
            });

            resolve(object);
        }, undefined, (error) => {
            reject(error);
        });
    });
}

export async function createPathBuilder() {
    try {
        let [obj, font] = await Promise.all([
            getModel(pathSolar),
            getFont(Roboto_Regular)
        ]);

        return new PathBuilder(obj, font);
    } catch (error) {
        console.error(error);
        throw error;
    };
}

const createShadedMaterial = (color: number) =>
    new THREE.MeshPhysicalMaterial({ color: color, reflectivity: 0.5, roughness: 0.5, metalness: 0.05, clearcoat: 0.5, side: THREE.DoubleSide });

const createFlagMaterial = (color: number, waveMask: Texture|null) =>
    new THREE.ShaderMaterial({
        ...flagShader(color, waveMask),
    });

export default class PathBuilder {
    private scale: number = 1;
    private flagMaterials: THREE.ShaderMaterial[] = [];
    private flagWaveMask: THREE.Texture|null = null;
    private startTime: number = Date.now();

    constructor(private templateGroup: THREE.Group, private font: THREE.Font) {
        const textureLoader = new TextureLoader();
        
        textureLoader.load(flagWaveMask, (texture) => {
            texture.wrapS = ClampToEdgeWrapping;
            texture.wrapT = ClampToEdgeWrapping;
            this.flagMaterials.forEach(m => m.uniforms[WAVE_MASK_TEXTURE_UNIFORM].value = texture);
            this.flagWaveMask = texture;
        });
    }

    createStart(x: number, y: number) {
        const model = this.templateGroup.getObjectByName('straight')!.clone() as THREE.Mesh;
        model.position.x = (x) * this.scale * 2 - this.scale * 101;
        model.position.z = y * this.scale * 2;
        model.rotation.y = 90 / 180 * Math.PI;
        model.scale.setScalar(this.scale);
        model.scale.z = this.scale * 100;
        model.material = new THREE.MeshPhysicalMaterial({ color: 0x1488CC, opacity: 0.5, reflectivity: 0.5, roughness: 0.5, metalness: 0.05, clearcoat: 0.5, side: THREE.DoubleSide });

        return model;
    }

    createMesh(type: string, rotation: number, material: THREE.Material) {
        const model = this.templateGroup.getObjectByName(type)!.clone() as THREE.Mesh;
        model.rotation.y = rotation / 180 * Math.PI;
        model.scale.setScalar(this.scale);
        model.material = material;

        return model;
    }

    createFlag(color: number) {
        const flagMaterial = createFlagMaterial(color, this.flagWaveMask);
        this.flagMaterials.push(flagMaterial);
        const flag = this.createMesh('flag', 90, flagMaterial);
        const stick = this.createMesh('pole', 0, createShadedMaterial(0xffffff));
        const anchor = this.createMesh('anchor', 0, createShadedMaterial(0xffffff));
        const parent = new THREE.Object3D();
        parent.add(flag);
        parent.add(stick);
        parent.add(anchor);
        return parent;
    }

    getHTML(params: PathElementData, y: number) {
        const shift = (params.textShift || 0) / 1.2;
        const elem = document.createElement("div");
        elem.className = "Timeline__label";
        // elem.style.color = "#" + params.color.toString(16);
        elem.style.color = `#fff`;
        elem.style.textTransform = 'uppercase';
        if (params.date !== '') {
            elem.innerHTML += `<h3 class="Timeline__label-date">${params.date}</h3>`;
        }
        if (params.header) {
            elem.innerHTML += `<h4 class="Timeline__label-header">${params.header}</h3>`;
        }
        if (params.description) {
            elem.innerHTML += `<p class="Timeline__label-description">${params.description}</p>`;
        }
        // elem.innerHTML += `<img src="${img1}" alt=""/>`
        var cssObject = new CSS3DObject(elem);
        cssObject.position.z = shift * this.scale * 2;
        cssObject.position.y = y * this.scale * 2;
        cssObject.rotation.y = 90 / 180 * Math.PI;
        cssObject.scale.setScalar(0.005 * this.scale);
        return cssObject;
    }

    createObject(params: PathElementData, x: number, z: number, rotation: number) {
        const metadata = TYPE_METADATA[params.type];
        let obj = new THREE.Object3D();

        const model = this.templateGroup.getObjectByName(metadata.templateName)!.clone() as THREE.Mesh;
        model.rotation.y = -(rotation + metadata.modelRotation) * Math.PI / 2;
        model.scale.setScalar(this.scale);
        model.material = new THREE.MeshPhysicalMaterial({
            color: params.color,
            reflectivity: 0.5,
            roughness: 0.5,
            metalness: 0.05,
            clearcoat: 0.5,
            side: THREE.DoubleSide
        });

        obj.add(model);

        if (params.flag) {
            obj.add(this.createFlag(params.color));
            obj.add(this.getHTML(params, 0.35));
        }

        obj.position.x = x * this.scale * 2;
        obj.position.z = z * this.scale * 2;
        return obj;
    }

    createPathObjects(params: PathElementData[]): PathNode[] {
        console.log(this.templateGroup.children);

        let x = -3;
        let z = -3;

        const elements: PathNode[] = [];
        elements.push({
            anchored: true,
            obj: this.createStart(x, z)
        });

        let rotation = 0;

        for (let i = 0; i < params.length; ++i) {
            let obj = this.createObject(params[i], x, z, rotation);
            const metadata = TYPE_METADATA[params[i].type];

            let x_change = Math.round(Math.cos(rotation * Math.PI / 2)) * metadata.forwardOffset +
                Math.round(Math.cos(rotation * Math.PI / 2 + Math.PI / 2)) * metadata.sideOffset;

            let z_change =  Math.round(Math.sin(rotation * Math.PI / 2)) * metadata.forwardOffset +
                Math.round(Math.sin(rotation * Math.PI / 2 + Math.PI / 2)) * metadata.sideOffset;

            x += x_change;
            z += z_change ;
            
            rotation += metadata.rotationOffset;
            elements.push({
                anchored: false,
                obj,
            });
        }

        return elements;
    }

    update() {
        this.flagMaterials.forEach(mat => {
            mat.uniforms[TIME_UNIFORM].value = (Date.now() - this.startTime) / 1000;
        });
    }
}
