import { Euler, Group, Mesh, Object3D, RepeatWrapping, ShaderMaterial, TextureLoader } from 'three';
import { AirflowRod, airflowRodMaterial } from './airflowRod';
import { OPACITY_UNIFORM, OVERLAY_TEXTURE_UNIFORM, WIDTH_TO_HEIGHT_RATIO_UNIFORM, planeShader, OVERLAY_UNIFORM } from './planeShader';
import cyjanBody from '../../assets/models/cyjan-body.obj';
import cyjanWings from '../../assets/models/cyjan-wings.obj';
import cyjanSolarPanels from '../../assets/models/cyjan-solar-panels.obj';
import blueprintGridPath from '../../assets/img/blueprint-grid.png';
import { SmoothScalar } from '../../utils/smoothScalar';
import { createModelLoader } from '../../utils/loaders';
import { Easing } from '../../utils/easing';

const bodyMaterial = new ShaderMaterial({
    ...planeShader([ 1, 1, 1 ]),
});

const accentMaterial = new ShaderMaterial({
    ...planeShader([ 1, 0, 0 ]),
});

const darkMaterial = new ShaderMaterial({
    ...planeShader([ 0, 0, 0 ]),
});

const wingsBodyMaterial = new ShaderMaterial({
    ...planeShader([ 0.9, 0.9, 0.9 ]),
});

const wingsAccentMaterial = new ShaderMaterial({
    ...planeShader([ 1, 0, 0 ]),
});

const wingsDarkMaterial = new ShaderMaterial({
    ...planeShader([ 0, 0, 0 ]),
});

const solarPanelMaterial = new ShaderMaterial({
    ...planeShader([ 0, 0, 0 ]),
});

const allMaterials = [
    bodyMaterial,
    accentMaterial,
    darkMaterial,
    wingsBodyMaterial,
    wingsAccentMaterial,
    wingsDarkMaterial,
];

const objLoader = createModelLoader();

export interface StorytellingPlaneConfig {
    planeScale?: number;
    overlayIntensity?: number;
    showWings?: boolean;
    showSolarPanels?: boolean;
    showAirflow?: boolean;
}

export class StorytellingPlane extends Object3D {
    private bodyGroup: Group = new Group();
    private wingsGroup: Group = new Group();
    private solarPanelsGroup: Group = new Group();
    private planeGroups: Group[];
    private rotorModel: Mesh|null = null;

    private airflowGroup: Group;
    private airflowRods: AirflowRod[] = [];

    private smoothScale = new SmoothScalar(0, 300, Easing.EASE_IN_OUT_SINE);
    private wingsOpacity = new SmoothScalar(0, 300, Easing.EASE_IN_OUT_SINE);
    private solarPanelsOpacity = new SmoothScalar(0, 300, Easing.EASE_IN_OUT_SINE);
    private airflowOpacity = new SmoothScalar(0, 400, Easing.EASE_IN_OUT_SINE);
    private overlayIntensity = new SmoothScalar(0, 400, Easing.EASE_IN_OUT_SINE);

    constructor() {
        super();

        this.planeGroups = [
            this.bodyGroup,
            this.wingsGroup,
            this.solarPanelsGroup,
        ];
        this.planeGroups.forEach(g => this.add(g));
        this.updatePositions();
        
        (async () => {
            const [ cyjanBodyScene, cyjanWingsScene, cyjanSolarPanelsScene ] = await Promise.all([
                objLoader.load(cyjanBody, {
                    materials: {
                        'Tail_Cylinder': darkMaterial,
                        'Body_Cube.001': bodyMaterial,
                        'Nose_Cube.002': darkMaterial,
                        'TailWing_Plane.002': bodyMaterial,
                        'TailWing.001_Plane.006': [
                            bodyMaterial,
                            accentMaterial,
                        ],
                    },
                }),
                objLoader.load(cyjanWings, {
                    materials: {
                        'Wings_Plane': [
                            wingsBodyMaterial,
                            wingsAccentMaterial,
                        ],
                    },
                }),
                objLoader.load(cyjanSolarPanels, {
                    materials: {
                        'Plane_Plane.007': solarPanelMaterial,
                    },
                }),
            ]);

            this.bodyGroup.add(cyjanBodyScene);
            this.wingsGroup.add(cyjanWingsScene);
            this.solarPanelsGroup.add(cyjanSolarPanelsScene);

            this.rotorModel = cyjanBodyScene.getObjectByName('Blades_Plane.001') as Mesh;
            this.rotorModel.material = darkMaterial;
        })();

        const textureLoader = new TextureLoader();
        
        textureLoader.load(blueprintGridPath, (texture) => {
            texture.wrapS = RepeatWrapping;
            texture.wrapT = RepeatWrapping;
            allMaterials.forEach(m => m.uniforms[OVERLAY_TEXTURE_UNIFORM].value = texture);
        });

        this.airflowGroup = new Group();
        this.airflowGroup.setRotationFromEuler(new Euler(Math.PI/4, -Math.PI/4, 0));
        this.add(this.airflowGroup);

        this.createAirflowRod(new AirflowRod(0), -0.1, 0.02);
        this.createAirflowRod(new AirflowRod(50), 0.1, 0.02);
        this.createAirflowRod(new AirflowRod(200), -0.02, -0.02);
        this.createAirflowRod(new AirflowRod(-100), 0.02, -0.05);
        this.createAirflowRod(new AirflowRod(500), -0.03, 0.05);
        this.createAirflowRod(new AirflowRod(-30), -0.07, -0.05);
    }

    setConfig(config: StorytellingPlaneConfig) {
        this.smoothScale.moveTo(config.planeScale !== undefined ? config.planeScale : 0);
        this.wingsOpacity.moveTo(config.showWings ? 1 : 0);
        this.solarPanelsOpacity.moveTo(config.showSolarPanels ? 1 : 0);
        this.airflowOpacity.moveTo(config.showAirflow ? 1 : 0);
        this.overlayIntensity.moveTo(config.overlayIntensity !== undefined ? config.overlayIntensity : 0);
    }

    createAirflowRod(rod: AirflowRod, x: number, y: number) {
        rod.position.set(x, y, 0);
        this.airflowGroup.add(rod);
        this.airflowRods.push(rod);
    }

    setWidthToHeightRatio(ratio: number) {
        allMaterials.forEach(m => m.uniforms[WIDTH_TO_HEIGHT_RATIO_UNIFORM].value = ratio);
    }

    updatePositions() {
        const now = Date.now();
        const x = Math.sin(now * 0.005) * 0.001 + Math.cos(now * 0.0131) * 0.0005;
        const y = Math.sin(now * 0.004 + 0.1234) * 0.001 + Math.cos(now * 0.0131 + 0.0151) * 0.0005;
        const roll = Math.sin(now * 0.0043) * 0.015 + Math.cos(now * 0.0121) * 0.0015;

        this.smoothScale.update();
        this.planeGroups.forEach(g => {
            g.position.set(x, y, 0);
            g.setRotationFromEuler(new Euler(Math.PI/4, -Math.PI/4, roll));
            g.scale.setScalar(this.smoothScale.value);
        });

        this.rotorModel?.setRotationFromEuler(new Euler(0, 0, now * 0.02));
    }

    onUpdate() {
        this.updatePositions();

        this.wingsOpacity.update();
        wingsBodyMaterial.uniforms[OPACITY_UNIFORM].value = this.wingsOpacity.value;
        wingsAccentMaterial.uniforms[OPACITY_UNIFORM].value = this.wingsOpacity.value;
        wingsDarkMaterial.uniforms[OPACITY_UNIFORM].value = this.wingsOpacity.value;

        this.solarPanelsOpacity.update();
        solarPanelMaterial.uniforms[OPACITY_UNIFORM].value = this.solarPanelsOpacity.value;

        this.overlayIntensity.update();
        allMaterials.forEach(m => m.uniforms[OVERLAY_UNIFORM].value = this.overlayIntensity.value);

        this.airflowOpacity.update();
        airflowRodMaterial.uniforms[OPACITY_UNIFORM].value = this.airflowOpacity.value;

        for (let rod of this.airflowRods) {
            rod.onUpdate();
        }
    }
}