import { Easing, easingFunctions } from './easing';

export class SmoothScalar {
    public t: number = 0;
    private from: number = 0;
    private to: number = 0;

    private lastTime: number = 0;

    constructor(
        initialValue: number,
        public duration: number = 500,
        public easing: Easing,
    ) {
        this.t = 0;
        this.from = initialValue;
        this.to = initialValue;
    }

    get value() {
        return this.from + easingFunctions[this.easing](this.t) * (this.to - this.from);
    }

    set value(v: number) {
        this.t = 0;
        this.from = v;
        this.to = v;
    }

    moveTo(value: number) {
        if (value === this.to) {
            return;
        }
        
        this.from = this.value;
        this.to = value;
        this.t = 0;
        this.lastTime = Date.now();
    }

    update() {
        const now = Date.now();
        const deltaTime = (now - this.lastTime) / this.duration;
        this.t = Math.min(this.t + deltaTime, 1);
        this.lastTime = now;
    }
}