import { DirectionalLight, Scene, Vector3 } from 'three';
import { Easing, easingFunctions } from '../../utils/easing';
import { getUnprojectedMousePos } from '../../utils/threeHelpers';
import Bubbles, { BubblesConfig } from '../bubbles/Bubbles';
import { BackgroundCamera } from '../common/backgroundCamera';
import { PageBackgroundSpecification } from '../common/effects/pageBackgroundPass';
import Particles from '../common/particles/Particles';
import { SectionBase } from '../context/sectionBase';
import { SectionController } from '../context/sectionController';
import { HomePageConfig, HomeSectionKey } from '../solarContext';
import { TimelineConfig } from '../timeline/timeline';
import { StorytellingPlane, StorytellingPlaneConfig } from './storytellingPlane';

export interface PlaneSectionConfig {
    plane?: StorytellingPlaneConfig;
    timeline?: TimelineConfig;
    bubbles?: BubblesConfig;
}

function createBackgroundEpicHandler(indices: number[]) {
    const spec: PageBackgroundSpecification = {
        primaryIndex: indices[0],
        secondaryIndex: 1,
        mix: 0,
    };

    return {
        getBackgroundSpec(progress: number): PageBackgroundSpecification {
            progress = Math.max(0, progress);
            
            const t = (progress * (indices.length - 1)) % 1;
            spec.mix = easingFunctions[Easing.EASE_IN_OUT_QUAD](t);

            const index = Math.min(Math.floor(progress * (indices.length - 1)), indices.length - 2);
            spec.primaryIndex = indices[index];
            spec.secondaryIndex = indices[index + 1];

            return spec;
        }
    };
}

const planeSectionBackgroundHandler = createBackgroundEpicHandler([
    1, 0, 2, 3,
]);

export class PlaneSection extends SectionBase implements SectionController<HomePageConfig, typeof HomeSectionKey.PLANE_SECTION> {
    public scene: Scene;
    public camera: BackgroundCamera;
    public airplane: StorytellingPlane;
    private particles: Particles;
    private bubbles: Bubbles;

    public backgroundSpec = {
        primaryIndex: 3,
        secondaryIndex: 2,
        mix: 0,
    };

    public updateCameraProjection(width: number, height: number) {
        const ratio = height / width;
        this.camera.bottom = -ratio / 2;
        this.camera.top = ratio / 2;
        this.camera.updateProjectionMatrix();
        this.airplane.setWidthToHeightRatio(ratio);

        this.particles.onResize();
    }

    constructor() {
        super();
        this.scene = new Scene();
        this.scene.background = null;
        this.camera = new BackgroundCamera(new Vector3(0, 0, 0), 0, 1, 1, 0, -100, 100);

        const sunLight = new DirectionalLight(0xffffff, 1);
        sunLight.position.set(0, 10, 0);
        sunLight.lookAt(0, 0, 0);
        this.scene.add(sunLight);

        this.particles = new Particles(window.innerWidth, window.innerHeight);
        this.particles.position.set(0.5, 0, -1);
        this.scene.add(this.particles);

        this.airplane = new StorytellingPlane();
        this.airplane.position.set(0.25, 0, 0);
        this.scene.add(this.airplane);

        this.bubbles = new Bubbles();
        this.bubbles.position.set(0.25, 0, 1);
        this.scene.add(this.bubbles);

        // Update the camera offset using the mouse position.
        window.addEventListener('mousemove', (e) => {
            let mouse = getUnprojectedMousePos(e, this.camera);

            this.particles.onMouseMove(e);
            this.bubbles.onMouseMove(e, mouse);
        });
    }

    setConfig(config: PlaneSectionConfig) {
        this.airplane.setConfig(config.plane || {});
        this.bubbles.setConfig(config.bubbles || {});
    }

    private animateParticles(delta: number, elapsedTime: number) {
        this.particles.onUpdate(delta, elapsedTime, this.progress);
        this.particles.points.lookAt(this.camera.position);
    }

    onUpdate(delta: number, elapsedTime: number) {
        this.airplane.onUpdate();
        this.bubbles.onUpdate(delta);
        this.animateParticles(delta, elapsedTime);
    }

    setSectionProgress(progress: number) {
        super.setSectionProgress(progress);
        const normalProgress = progress / this.sectionHeight;
        this.backgroundSpec = planeSectionBackgroundHandler.getBackgroundSpec(normalProgress);
    }
}
