import React from 'react';
import './TeamCard.scss';

export interface TeamCardProps {
    heading: string;
    img: string;
    // desc: string;
    members: number;
    coordinator: string;
    background?: string;
    children?: React.ReactNode;
}

export function TeamCard(props: TeamCardProps) {
    return (
        <div className="TeamCard" style={{background: props.background}}>
            
            <div className="TeamCard__img-block" style={{background: props.background}}>
                <img className="TeamCard__img" src={props.img} alt={props.heading}/>
                {/* <img className="TeamCard__img-background" src={logo}/> */}
                
            </div>
            <div className="TeamCard__content">
                <h3 className="TeamCard__heading">{props.heading}</h3>
                <p className="TeamCard__description">
                    {props.children}
                </p>
            </div>
            <div className="TeamCard__stats-container" style={{background: props.background}}>
                <div className="TeamCard__stat">
                    <span className="TeamCard__value TeamCard__value--big">{props.members}</span>
                    <span className="TeamCard__name">czonkw</span>
                </div>
                <div className="TeamCard__stat TeamCard__stat--big">
                    <span className="TeamCard__value">{props.coordinator}</span>
                    <span className="TeamCard__name">Koordynator</span>
                </div>
            </div>
        </div>
    );
}
