export enum Easing {
    LINEAR,
	EASE_IN_QUAD,
    EASE_OUT_QUAD,
	EASE_IN_CUBIC,
	EASE_OUT_CUBIC,
	EASE_IN_OUT_QUAD,
    EASE_IN_OUT_SINE,
}

export const easingFunctions = {
	[Easing.LINEAR]: (t: number) => t,
	[Easing.EASE_IN_QUAD]: (t: number) => t * t,
    [Easing.EASE_OUT_QUAD]: (t: number) => {
        const it = 1 - t;
        return 1 - it * it;
    },
    [Easing.EASE_IN_CUBIC]: (t: number) => t * t * t,
    [Easing.EASE_OUT_CUBIC]: (t: number) => {
        const it = 1 - t;
        return 1 - it * it * it;
	},
	[Easing.EASE_IN_OUT_QUAD]: (t: number) => {
		t *= 2;
		if (t < 1)
			return t * t / 2;
		t--;
		return -(t * (t - 2) - 1) / 2;
	},
    [Easing.EASE_IN_OUT_SINE]: (t: number) => {
        return 0.5 - Math.cos(t * Math.PI) * 0.5;
    },
};