import { ListenerRemover, SectionEventListener } from './sectionBase';
import { SectionController } from './sectionController';

export type PageMap<P> = {
    [k in keyof P]: P[k]
};

export type ThreePageConfig<I extends object> = {
    [k in keyof I]?: I[k]
};

export interface Breakpoint<I extends ThreePageConfig<I>, K extends keyof I> {
    progress: number;
    section: SectionController<I, K>;
}

export class ThreePage<C extends ThreePageConfig<C>> {
    private sectionMap!: { [k in keyof C]: SectionController<C, k> };
    private breakpoints: Breakpoint<C, keyof C>[] = [];

    private pageProgress: number = 0;

    private _currentBreakpoint: Breakpoint<C, keyof C> | null = null;
    private _nextBreakpoint: Breakpoint<C, keyof C> | null = null;

    constructor() {
        this.sectionMap = {} as any;
    }

    get progress() {
        return this.pageProgress;
    }

    get currentBreakpoint(): Breakpoint<C, keyof C> | null {
        return this._currentBreakpoint;
    }

    get nextBreakpoint(): Breakpoint<C, keyof C> | null {
        return this._nextBreakpoint;
    }

    private computeBreakpoints() {
        this.breakpoints = [];
        
        let accumulatedProgress = 0;
        for (const sectionKey in this.sectionMap) {
            const section = this.sectionMap[sectionKey];

            this.breakpoints.push({
                progress: accumulatedProgress,
                section: section,
            });
            accumulatedProgress += section.sectionHeight;
        }

        // Updating the current and next breakpoints.
        this.computeCurrentAndNextBreakpoints();
    }

    public setProgress(progress: number) {
        this.pageProgress = progress;
        this.computeCurrentAndNextBreakpoints();
    }

    public setConfig(config: C) {
        for (const key in config) {
            if (this.sectionMap[key]) {
                this.sectionMap[key].setConfig(config[key] || {});
            } else {
                console.error(`Did you forget to add the '${key}' section to a page while initializing your ThreeContext?`);
            }
        }
    }

    public setSectionHeight<K extends keyof C>(sectionKey: K, height: number) {
        const section = this.sectionMap[sectionKey];
        if (section) {
            if (section.sectionHeight !== height) {
                section.setSectionHeight(height);
                this.computeBreakpoints();
            }
        }
        else {
            console.error(`Tries to set the height of a section that doesn't exist: ${sectionKey}`);
        }
    }

    public registerSection<K extends keyof C>(sectionKey: K, controller: SectionController<C, K>): void {
        this.sectionMap[sectionKey] = controller;
        this.computeBreakpoints();
    }

    public addListener<K extends keyof C>(sectionKey: K, event: string, listener: SectionEventListener): ListenerRemover {
        return this.sectionMap[sectionKey]?.addListener(event, listener) || (() => {});
    }

    public updateCameraProjection(width: number, height: number) {
        for (const breakpoint of this.breakpoints) {
            breakpoint.section.updateCameraProjection(width, height);
        }
    }

    private computeCurrentAndNextBreakpoints() {
        for (let i = this.breakpoints.length - 1; i >= 0; --i) {
            const breakpoint = this.breakpoints[i];

            if (this.pageProgress >= breakpoint.progress) {
                this._currentBreakpoint = breakpoint;
                this._nextBreakpoint = this.breakpoints[i + 1] || null;

                if (breakpoint.section.setSectionProgress)
                    breakpoint.section.setSectionProgress(this.pageProgress - breakpoint.progress);

                if (this._nextBreakpoint && this._nextBreakpoint.section.setSectionProgress)
                    this._nextBreakpoint.section.setSectionProgress(this.pageProgress - this._nextBreakpoint.progress);

                return;
            }
        }

        this._currentBreakpoint = null;
        this._nextBreakpoint = null;
    }
}

