import {
    BufferGeometry,
    Clock,
    Float32BufferAttribute,
    Group,
    LinearFilter,
    Mesh,
    OrthographicCamera,
    PointLight,
    RepeatWrapping,
    Scene,
    ShaderMaterial,
    TextureLoader,
    WebGLRenderer,
} from 'three';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer';
import { SSAARenderPass } from 'three/examples/jsm/postprocessing/SSAARenderPass';
import { OVERLAY_COLOR_UNIFORM, sectionAreaShader, TEXTURE_UNIFORM } from './sectionAreaShader';
import { ThreePage } from './threePage';
import { SectionController } from './sectionController';
import gradientSheetPath from '../../assets/img/gradient-sheet.png';
import { BlurPass } from '../common/effects/blurPass';
import { PageTransition, TransitionContext } from './transition';
import { createFadeTransition } from './fadeTransition';
import { SectionDisplay } from './sectionDisplay';
import { PageBackgroundPass } from '../common/effects/pageBackgroundPass';
import { ListenerRemover, SectionEventListener } from './sectionBase';

export type ThreeContextConfig<I extends object> = {
    [k in keyof I]: I[k]
};

function createPlane() {
    const geometry = new BufferGeometry();
    const vertices = [
        0, 1, 0,
        1, 1, 0,
        1, 0, 0,
        0, 0, 0,
    ];
    const uvs = [
        0, 0,
        1, 0,
        1, 1,
        0, 1,
    ];
    const indices = [
        0, 1, 3,
        1, 2, 3,
    ];

    geometry.setAttribute('position', new Float32BufferAttribute(vertices, 3));
    geometry.setAttribute('uv', new Float32BufferAttribute(uvs, 2));
    geometry.setIndex(indices);
    return geometry;
}

function createPlaneTail() {
    const geometry = new BufferGeometry();
    const vertices = [
        1, -0.1, 0,
        0, 0, 0,
        1, 0, 0,
    ];
    const indices = [
        0, 1, 2,
    ];

    geometry.setAttribute('position', new Float32BufferAttribute(vertices, 3));
    geometry.setIndex(indices);
    return geometry;
}

export class ThreeContext<C extends ThreeContextConfig<C>> implements TransitionContext<C> {
    private primarySectionMaterial = new ShaderMaterial({
        ...sectionAreaShader(),
    });
    
    private secondarySectionMaterial = new ShaderMaterial({
        ...sectionAreaShader(),
    });

    /**
     * This element contains the canvas.
     */
    private containerElement: HTMLElement;
    private renderer: WebGLRenderer;

    public primarySectionDisplay: SectionDisplay;
    public overlapSectionDisplay: SectionDisplay;
    public composer: EffectComposer;
    private backgroundOverlayPass!: PageBackgroundPass;
    public scene: Scene;
    public camera: OrthographicCamera;
    public clock: Clock;
    public primaryPlane: Mesh;
    public nextOverlapPlane: Mesh;
    public nextOverlapTail: Mesh;
    public nextOverlapGroup: Group;

    private pages: { [k in keyof C]: ThreePage<C[k]> } = {} as any;
    public prevPage: ThreePage<any> | null = null;
    public currentPage: ThreePage<any> | null = null;
    private pageTransition: PageTransition<C> | null = null;

    constructor() {
        this.renderer = new WebGLRenderer();
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.setClearColor(0xffff00ff);
        this.renderer.sortObjects = false;

        this.composer = new EffectComposer(this.renderer);
        this.composer.setPixelRatio(window.devicePixelRatio);

        this.scene = new Scene();
        this.camera = new OrthographicCamera(0, 1, 1, 0, -100, 100);
        this.clock = new Clock();

        this.primarySectionDisplay = new SectionDisplay(this.renderer, this.scene, this.camera);
        this.overlapSectionDisplay = new SectionDisplay(this.renderer, this.scene, this.camera);
        this.primarySectionDisplay.setPixelRatio(window.devicePixelRatio);
        this.overlapSectionDisplay.setPixelRatio(window.devicePixelRatio);

        const textureLoader = new TextureLoader();

        textureLoader.load(gradientSheetPath, (texture) => {
            texture.flipY = false;
            texture.wrapS = RepeatWrapping;
            texture.wrapT = RepeatWrapping;
            texture.minFilter = LinearFilter;
            texture.magFilter = LinearFilter;

            const blurPass = new BlurPass({
                hBlur: 0.01,
                vBlur: 0.01,
            });

            this.primarySectionDisplay.constructPasses(texture, blurPass);
            this.overlapSectionDisplay.constructPasses(texture, blurPass);
        });

        this.composer.addPass(new SSAARenderPass(this.scene, this.camera, 0xffffff, 1));

        this.containerElement = document.createElement('div');
        this.containerElement.id = 'threejs-container';
        document.body.appendChild(this.containerElement);

        this.primaryPlane = new Mesh(createPlane(), this.primarySectionMaterial);
        this.primarySectionMaterial.uniforms[TEXTURE_UNIFORM].value = this.primarySectionDisplay.outputTexture;
        this.primaryPlane.position.set(0, 0, 0);
        this.scene.add(this.primaryPlane);

        this.secondarySectionMaterial.uniforms[TEXTURE_UNIFORM].value = this.overlapSectionDisplay.outputTexture;
        this.nextOverlapGroup = new Group();
        this.scene.add(this.nextOverlapGroup);

        this.nextOverlapPlane = new Mesh(createPlane(), this.secondarySectionMaterial);
        this.nextOverlapGroup.add(this.nextOverlapPlane);
        this.nextOverlapTail = new Mesh(createPlaneTail(), this.secondarySectionMaterial);
        this.nextOverlapGroup.add(this.nextOverlapTail);

        const light = new PointLight(0xffffffff);
        light.position.set(0, 0, -2);
        this.scene.add(light);

        this.containerElement.appendChild(this.renderer.domElement);
        this.updateCameraProjection(window.innerWidth, window.innerHeight);

        // Resize the canvas on window resize.
        window.addEventListener('resize', (e) => {
            this.updateCameraProjection(window.innerWidth, window.innerHeight);
        }, false);

        // Setting up the render loop
        const renderLoop = () => {
            this.render();
            requestAnimationFrame(renderLoop);
        };

        renderLoop();
    }

    private updateCameraProjection(width: number, height: number) {
        const ratio = height / width;
        this.camera.bottom = ratio;
        this.camera.top = 0;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(width, height);
        this.composer.setSize(width, height);
        this.primarySectionDisplay.setSize(width, height);
        this.overlapSectionDisplay.setSize(width, height);

        this.primaryPlane.scale.set(1, ratio, 1);
        this.nextOverlapPlane.scale.set(1, ratio, 1);

        for (const key in this.pages) {
            this.pages[key].updateCameraProjection(width, height);
        }
    }

    public setProgress(progress: number) {
        if (this.currentPage) {
            this.currentPage.setProgress(progress);
        }
    }

    public setPage<K extends keyof C>(page: K) {
        if (this.currentPage === null) {
            this.prevPage = this.currentPage = this.pages[page] || null;
        }
        else {
            this.prevPage = this.currentPage;
            this.currentPage = this.pages[page] || null;
            this.pageTransition = createFadeTransition(500);
        }

        if (this.currentPage === null) {
            console.error(`Couldn't find a page with key '${page}'.`);
            this.pageTransition = null;
        }
        else {
            console.log(`Setting page to ${page}`);
        }
    }

    public setConfig<K extends keyof C>(pageKey: K, config: C[K]) {
        if (this.pages[pageKey]) {
            this.pages[pageKey].setConfig(config);
        }
    }

    public setSectionHeight<K extends keyof C, KS extends keyof C[K]>(pageKey: K, sectionKey: KS, height: number) {
        if (this.pages[pageKey]) {
            this.pages[pageKey].setSectionHeight(sectionKey, height);
        }
        else {
            console.error(`Tries to set the height of a section whose page doesn't exist: ${pageKey}`);
        }
    }
    
    public registerSection<K extends keyof C, KS extends keyof C[K]>(pageKey: K, sectionKey: KS, controller: SectionController<C[K], KS>): void {
        if (this.pages[pageKey] === undefined) {
            this.pages[pageKey] = new ThreePage();
        }

        this.pages[pageKey].registerSection(sectionKey, controller);
        this.pages[pageKey].updateCameraProjection(window.innerWidth, window.innerHeight);
    }

    public addListener<K extends keyof C, KS extends keyof C[K]>(pageKey: K, sectionKey: KS, event: string, listener: SectionEventListener): ListenerRemover {
        if (this.pages[pageKey] === undefined) {
            this.pages[pageKey] = new ThreePage();
        }

        return this.pages[pageKey].addListener(sectionKey, event, listener);
    }

    render() {
        if (this.currentPage === null) {
            return;
        }

        if (this.pageTransition !== null && this.prevPage !== null) {
            if (!this.pageTransition.perform(this, this.prevPage, this.currentPage)) {
                // Rendering
                this.composer.render();

                return;
            }

            this.primarySectionMaterial.uniforms[OVERLAY_COLOR_UNIFORM].value = [0, 0, 0, 0];
            this.secondarySectionMaterial.uniforms[OVERLAY_COLOR_UNIFORM].value = [0, 0, 0, 0];
            this.primarySectionDisplay.resetTransition();
            this.overlapSectionDisplay.resetTransition();
            this.pageTransition = null;
            this.prevPage = null;
        }

        const current = this.currentPage.currentBreakpoint;
        const next = this.currentPage.nextBreakpoint;

        if (next) {
            this.overlapPosition = next.progress - this.currentPage.progress;
        }

        if (!current) {
            throw new Error(`No breakpoint defined for the current page.`);
        }

        this.showSections(current.section, next?.section);

        // Rendering
        this.composer.render();
    }

    /**
     * This is public because it's used by both transitions and the regular render-loop.
     * @param primarySection The section to be rendered as primary (top)
     * @param secondarySection The section to be rendered as secondary (bottom)
     */
    public showSections(primarySection: SectionController<any, any>, secondarySection?: SectionController<any, any>): void {
        let delta = this.clock.getDelta();
        let elapsedTime = this.clock.getElapsedTime();

        primarySection.onUpdate(delta, elapsedTime);
        this.primarySectionDisplay.render(primarySection);

        if (secondarySection) {
            secondarySection.onUpdate(delta, elapsedTime);
            this.overlapSectionDisplay.render(secondarySection);

            this.nextOverlapGroup.visible = true;
        }
        else {
            this.nextOverlapGroup.visible = false;
        }
    }

    set overlapPosition(position: number) {
        this.nextOverlapGroup.position.set(0, position / window.innerWidth, 0);

        const page = this.prevPage || this.currentPage;
        if (page !== null && page.nextBreakpoint) {
            const windowProgress = position / window.innerHeight;
            this.nextOverlapTail.scale.set(1, 1 - windowProgress, 1);
        }
    }
}