import React, { useRef } from 'react';
import classNames from 'classnames';
import './SideStoryPanel.scss';
import { useIsVisible } from '../../hooks/useIsVisible';

export interface SideStoryPanelProps {
    title: string;
    marginTop?: number;
    marginBottom?: number;
    children: React.ReactNode;
}

export function SideStoryPanel(props: SideStoryPanelProps) {
    const ref = useRef<HTMLDivElement>(null);
    const isVisible = useIsVisible(ref, 400);

    return (
        <div ref={ref} className={classNames('SideStoryPanel', {
                visible: isVisible,
            })}
            style={{
                paddingTop: `${props.marginTop || 0}px`,
                paddingBottom: `${props.marginBottom || 0}px`,
            }}
        >
            <div className="SideStoryPanel--panel">
                <h1 className="SideStoryPanel--title">{props.title}</h1>
                <div className="SideStoryPanel--description">
                    {props.children}
                </div>
            </div>
        </div>
    );
}
