import React, { useContext } from 'react';
import { createContext } from 'react';
import { SectionController } from '../../3d/context/sectionController';
import { ThreeContextContext } from './ThreeContextProvider';

export interface PageProps {
    pageKey: string;
    children: React.ReactNode;
}

interface Context {
    pageKey: string;
    registerSection(key: string, controller: SectionController<any, any>): void;
}

export const PageContext = createContext<Context|null>(null);

export function Page(props: PageProps) {
    const threeContext = useContext(ThreeContextContext);

    return (
        <PageContext.Provider value={{
            pageKey: props.pageKey,
            registerSection(sectionKey: string, controller: SectionController<any, any>) {
                if (threeContext) {
                    threeContext.registerSection(props.pageKey, sectionKey, controller);
                }
                else {
                    console.error(`Couldn't register a section without a ThreeContext to attach to.`);
                }
            }
        }}>
            {props.children}
        </PageContext.Provider>
    );
}