import React, { useRef } from 'react';
import classNames from 'classnames';
import './MiddlePanel.scss';
import { useIsVisible } from '../../hooks/useIsVisible';

export interface MiddlePanelProps {
    title: string;
    marginTop?: number;
    marginBottom?: number;
    children: React.ReactNode;
}

export function MiddlePanel(props: MiddlePanelProps) {
    const ref = useRef<HTMLDivElement>(null);
    const isVisible = useIsVisible(ref, 400);

    return (
        <div ref={ref} className={classNames('MiddlePanel', {
                visible: isVisible,
            })}
            style={{
                paddingTop: `${props.marginTop || 0}px`,
                paddingBottom: `${props.marginBottom || 0}px`,
            }}
        >
            <div className="MiddlePanel--panel">
                <h1 className="MiddlePanel--title">{props.title}</h1>
                
                {props.children}
            </div>
        </div>
    );
}
