import React from 'react';
import './DefaultCard.scss';

export interface DefaultCardProps {
    heading: string;
    subheading?: string;
    footer?: string;
    background?: string;
    children?: React.ReactNode;
    className?: string;
    left?:string;
}

export function DefaultCard(props: DefaultCardProps) {
    return (
        <div className={"DefaultCard "+ props.className} style={{background: props.background, left: props.left}}>
            <header className="DefaultCard__header">
                <h3 className="DefaultCard__heading">{props.heading}</h3>
                <h4 className="DefaultCard__subheading">{props.subheading}</h4>
            </header>
            <div className="DefaultCard__content">
                
                <p className="DefaultCard__description">
                    {props.children}
                </p>
            </div>
            <footer className="DefaultCard__footer">
                {props.footer}
            </footer>
        </div>
    );
}
