import { Camera, Object3D, Vector2, Vector3 } from "three";

function putMousePos(e: MouseEvent, mouse: Vector2) {
    mouse.x = (e.clientX / window.innerWidth) * 2 - 1;
    mouse.y = - (e.clientY / window.innerHeight) * 2 + 1;
}

export function getMousePos(e: MouseEvent): Vector2 {
    let mouse = new Vector2();
    putMousePos(e, mouse)
    return mouse;
}

export function getUnprojectedMousePos(e: MouseEvent, camera: Camera) {
    let pos = getMousePos(e);
    let mouse = new Vector3(pos.x, pos.y, 0);
    mouse.unproject(camera);
    return mouse;
}

export function worldToLocalMouse(unprojectedMouse: Vector3, localObj: Object3D) {
    let mouse = unprojectedMouse.clone();
    localObj.worldToLocal(mouse);
    mouse.z = 0;
    return mouse;
}