import {
    Material,
	NearestFilter,
	ShaderMaterial,
	UniformsUtils,
    WebGLRenderer,
	WebGLRenderTarget
} from 'three';
import { Pass } from 'three/examples/jsm/postprocessing/Pass';
import { HorizontalBlurShader } from 'three/examples/jsm/shaders/HorizontalBlurShader';
import { VerticalBlurShader } from 'three/examples/jsm/shaders/VerticalBlurShader';

interface BlurPassParams {
    hBlur: number;
    vBlur: number;
}

export class BlurPass extends Pass {

    private hQuad: Pass.FullScreenQuad;
    private vQuad: Pass.FullScreenQuad;

    private renderTargetHorizontal: WebGLRenderTarget;
    private hBlur: number;
    private vBlur: number;

    private horizontalUniforms: any;
    private verticalUniforms: any;

    private hQuadMaterial: Material;
    private vQuadMaterial: Material;

    constructor(params: BlurPassParams) {
        // render targets
        super();

        this.hBlur = params.hBlur;
        this.vBlur = params.vBlur;

        this.renderTargetHorizontal = new WebGLRenderTarget(1, 1, {
            minFilter: NearestFilter,
            magFilter: NearestFilter
        });

        this.renderTargetHorizontal.texture.name = 'BlurPass.middle';

        // bokeh material
        const horizontalBlurShader = HorizontalBlurShader;
        const verticalBlurShader = VerticalBlurShader;
        this.horizontalUniforms = UniformsUtils.clone(horizontalBlurShader.uniforms);
        this.verticalUniforms = UniformsUtils.clone(verticalBlurShader.uniforms);

        this.horizontalUniforms['h'].value = this.hBlur;
        this.verticalUniforms['tDiffuse'].value = this.renderTargetHorizontal.texture;
        this.verticalUniforms['v'].value = this.vBlur;

        this.hQuadMaterial = new ShaderMaterial({
            defines: {},
            uniforms: this.horizontalUniforms,
            vertexShader: horizontalBlurShader.vertexShader,
            fragmentShader: horizontalBlurShader.fragmentShader,
        });

        this.vQuadMaterial = new ShaderMaterial({
            defines: {},
            uniforms: this.verticalUniforms,
            vertexShader: verticalBlurShader.vertexShader,
            fragmentShader: verticalBlurShader.fragmentShader,
        });

        this.needsSwap = true;
        this.hQuad = new Pass.FullScreenQuad(this.hQuadMaterial);
        this.vQuad = new Pass.FullScreenQuad(this.vQuadMaterial);
    }

    setSize(width: number, height: number) {
        this.renderTargetHorizontal.setSize(width, height);
    }

    render(renderer: WebGLRenderer, writeBuffer: WebGLRenderTarget, readBuffer: WebGLRenderTarget/*, deltaTime, maskActive*/) {
		// Render horizontal blur into texture
		const oldAutoClear = renderer.autoClear;
		renderer.autoClear = false;

        this.horizontalUniforms['tDiffuse'].value = readBuffer.texture;
		renderer.setRenderTarget(this.renderTargetHorizontal);
        renderer.clear();
        this.hQuad.material = this.hQuadMaterial;
		this.hQuad.render(renderer);

        // Render blur composite
        this.vQuad.material = this.vQuadMaterial;

		if (this.renderToScreen) {
            renderer.setRenderTarget(null);
            renderer.clear();
            this.vQuad.render(renderer);
        }
        else {
			renderer.setRenderTarget(writeBuffer);
            renderer.clear();
			this.vQuad.render(renderer);
		}

		renderer.autoClear = oldAutoClear;
	}
};