// eslint-disable-next-line
import vertexShader from '!raw-loader!./plane.vert';
// eslint-disable-next-line
import fragmentShader from '!raw-loader!./plane.frag';

export const WIDTH_TO_HEIGHT_RATIO_UNIFORM: string = 'uWidthToHeightRatio';
export const COLOR_UNIFORM: string = 'uColor';
export const OPACITY_UNIFORM: string = 'uOpacity';
export const OVERLAY_UNIFORM: string = 'uOverlay';
export const OVERLAY_TEXTURE_UNIFORM: string = 'uOverlayTexture';

export const planeShader = (color: [number, number, number], opacity: number = 1) => ({
    uniforms: {
        [WIDTH_TO_HEIGHT_RATIO_UNIFORM]: { value: 1 },
        [COLOR_UNIFORM]: { value: color },
        [OPACITY_UNIFORM]: { value: opacity },
        [OVERLAY_UNIFORM]: { value: 0 },
        [OVERLAY_TEXTURE_UNIFORM]: { type: 't', value: undefined },
    },
    vertexShader: vertexShader,
    fragmentShader: fragmentShader,
    transparent: true,
});
