import { Texture, WebGLRenderer, Scene, Camera } from 'three';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer';
import { BlurPass } from '../common/effects/blurPass';
import { PageBackgroundPass, PageBackgroundSpecification } from '../common/effects/pageBackgroundPass';
import { RenderWithOpacityPass } from '../common/effects/renderWithOpacityPass';
import { SectionController } from './sectionController';

export class SectionDisplay {
    private composer: EffectComposer;
    
    private renderPass: RenderWithOpacityPass;
    private backgroundPass!: PageBackgroundPass;

    private inTransition: boolean = false;

    constructor(renderer: WebGLRenderer, scene: Scene, camera: Camera) {
        this.composer = new EffectComposer(renderer);
        this.composer.renderToScreen = false;
        
        this.renderPass = new RenderWithOpacityPass(scene, camera, 1.0);
        this.renderPass.clear = false;
    }

    get outputTexture(): Texture {
        return this.composer.renderTarget2.texture;
    }

    constructPasses(gradientSheet: Texture, blurPass: BlurPass): void {
        this.backgroundPass = new PageBackgroundPass(gradientSheet, 16 / gradientSheet.image.height);

        this.composer.addPass(this.backgroundPass);
        this.composer.addPass(blurPass);
        this.composer.addPass(this.renderPass);
    }

    setSize(width: number, height: number): void {
        this.composer.setSize(width, height);
    }

    setPixelRatio(pixelRatio: number): void {
        this.composer.setPixelRatio(pixelRatio);
    }

    setSceneOpacity(opacity: number): void {
        this.renderPass.opacity = opacity;
    }

    setTransition(specFrom: PageBackgroundSpecification, specTo: PageBackgroundSpecification, progress: number) {
        this.backgroundPass.setTransitionSpecification(specFrom, specTo, progress);
        this.inTransition = true;
    }

    resetTransition() {
        this.inTransition = false;
        this.setSceneOpacity(1);
    }

    render(section: SectionController<any, any>): void {
        this.renderPass.scene = section.scene;
        this.renderPass.camera = section.camera;

        if (this.backgroundPass) {
            if (!this.inTransition) {
                this.backgroundPass.setSpecification(section.backgroundSpec);
            }
        }

        this.composer.render();
    }
}