import React, { useContext, useEffect, useMemo, useRef } from 'react';
import { PageContext } from '../Page';
import { ScrollContext } from '../ScrollContext';
import { ThreeContextContext } from '../ThreeContextProvider';

export enum NodePhase {
    HIDDEN,
    ACTIVE,
}

export interface NodeState {
    nodeProgress: number;
    phase: NodePhase;
}

export interface StorytellerNodeProps<C> {
    children: React.ReactNode | React.ReactNode[] | ((state: NodeState, context: C) => React.ReactNode | React.ReactNode[]);
    config?: C;
}

function useNodeState(elementRef: React.RefObject<HTMLDivElement>): NodeState {
    // Used to update the state only when the user scrolls changes.
    const { scrollTop, viewHeight } = useContext(ScrollContext);

    const state = useMemo<NodeState>(() => {
        const distanceFromBottom = 600;
        const rect = elementRef.current?.getBoundingClientRect();

        if (rect) {
            const breakpoint = viewHeight - distanceFromBottom;
            if (rect.y < breakpoint && rect.bottom >= breakpoint) {
                return {
                    nodeProgress: rect.y / rect.height,
                    phase: NodePhase.ACTIVE,
                };
            } else {
                return {
                    nodeProgress: 0,
                    phase: NodePhase.HIDDEN,
                };
            }
        }

        return {
            nodeProgress: 0,
            phase: NodePhase.HIDDEN,
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [ scrollTop, viewHeight, elementRef ]);

    return state;
}

export function createStorytellerNode<C>() {
    return ({ children, config }: StorytellerNodeProps<C>) => {
        const threeContext = useContext(ThreeContextContext);
        const pageContext = useContext(PageContext);
        const pageKey = useMemo(() => pageContext?.pageKey || null, [ pageContext ]);

        const elementRef = useRef<HTMLDivElement>(null);

        const state = useNodeState(elementRef);
        const phase = state.phase;

        useEffect(() => {
            if (phase === NodePhase.ACTIVE && pageKey && threeContext) {
                threeContext.setConfig(pageKey, config);
            }
        }, [ pageKey, threeContext, config, phase ]);

        return (
            <div ref={elementRef}>
                {typeof children === 'function' ? children(state, threeContext as unknown as C) : children}
            </div>
        );
    };
}