// eslint-disable-next-line
import vertexShader from '!raw-loader!./background.vert';
// eslint-disable-next-line
import fragmentShader from '!raw-loader!./background.frag';

export enum Uniforms {
    OPACITY = 'uOpacity',
    GRADIENT_SHEET_UNIFORM = 'uTextureSheet',
    FRAME_MIX_UNIFORM = 'uTextureMix',
    FRAME_TARGET_MIX_UNIFORM = 'uTextureTargetMix',
    PAGE_TRANSITION_MIX_UNIFORM = 'uPageTransitionMix',
    PRIMARY_INDEX = 'uPrimaryIndex',
    SECONDARY_INDEX = 'uSecondaryIndex',
    PRIMARY_TARGET_INDEX = 'uPrimaryTargetIndex',
    SECONDARY_TARGET_INDEX = 'uSecondaryTargetIndex',
    FRAME_HEIGHT = 'uFrameHeight',
}

export const pageBackgroundShader = () => ({
    uniforms: {
        [Uniforms.GRADIENT_SHEET_UNIFORM]: { type: 't', value: undefined },
        [Uniforms.FRAME_MIX_UNIFORM]: { value: 0 },
        [Uniforms.FRAME_TARGET_MIX_UNIFORM]: { value: 0 },
        [Uniforms.PAGE_TRANSITION_MIX_UNIFORM]: { value: 0 },
        [Uniforms.PRIMARY_INDEX]: { value: 0 },
        [Uniforms.SECONDARY_INDEX]: { value: 0 },
        [Uniforms.PRIMARY_TARGET_INDEX]: { value: 0 },
        [Uniforms.SECONDARY_TARGET_INDEX]: { value: 0 },
        [Uniforms.FRAME_HEIGHT]: { value: 1 },
    },
    vertexShader: vertexShader,
    fragmentShader: fragmentShader,
});
