import React, { MutableRefObject, useEffect, useMemo, useRef, useState } from 'react';
import classNames from 'classnames';
import './ScrollView.scss';

interface RodProps {
    className?: string;
    widgetTop?: string;
    widgetBottom?: string;
}

function Rod({ className, widgetTop, widgetBottom }: RodProps) {
    return (
        <div className={classNames(className, 'ScrollView__Rod')} style={{
            top: widgetTop,
            bottom: widgetBottom,
        }}>
            <div className="ScrollView__Rod__vertical"></div>
            <div className="ScrollView__Rod__cross"></div>
        </div>
    );
}

interface ScrollHandleProps {
    progress: number;
    barHeight: number;
    transitionDuration: number;
    onMouseDown?: React.MouseEventHandler;
    onMouseMove?: React.MouseEventHandler;
    onMouseUp?: React.MouseEventHandler;
}

function ScrollHandle(props: ScrollHandleProps) {
    return (
        <div className="ScrollView__handle"
            style={{
                top: `${props.progress  * 100}%`,
                height: `${props.barHeight * 100}%`,
                transition: `${props.transitionDuration}s ease-out top`,
            }}
            onMouseDown={props.onMouseDown}
            onMouseMove={props.onMouseMove}
            onMouseUp={props.onMouseUp}
        />
    );
}

export interface ScrollViewProps {
    style?: React.CSSProperties;
    widgetTop: number;
    widgetBottom: number;
    scrollTop: number;
    children: React.ReactNode;
    scrollableRef: MutableRefObject<HTMLDivElement|null>;
}

export function ScrollView(props: ScrollViewProps) {
    const { widgetTop, widgetBottom, scrollTop, scrollableRef } = props;
    
    const contentsRef = useRef<HTMLDivElement>(null);

    const progress = useMemo(() => {
        if (contentsRef.current === null) {
            return 0;
        }

        const rect = contentsRef.current.getBoundingClientRect();
        return scrollTop / (rect.height);
    }, [contentsRef, scrollTop]);

    const barHeight = useMemo(() => {
        if (contentsRef.current === null || scrollableRef.current === null) {
            return 0;
        }

        const rect = contentsRef.current.getBoundingClientRect();
        const viewHeight = scrollableRef.current.getBoundingClientRect().height;
        return viewHeight / rect.height;
    }, [contentsRef, scrollableRef, scrollTop]);

    const [handleHeld, setHandleHeld] = useState(false);
    const prevMouseY = useRef<number>(0);

    useEffect(() => {
        const handleMouseMove = (e: MouseEvent) => {
            e.stopPropagation();
            e.preventDefault();

            const diffY = e.clientY - prevMouseY.current;
            
            if (contentsRef.current && scrollableRef.current) {
                const contentHeight = contentsRef.current.getBoundingClientRect().height;
                scrollableRef.current.scrollTop += diffY * contentHeight / (window.innerHeight - widgetBottom - widgetTop);
            }

            prevMouseY.current = e.clientY;
        };
    
        const handleMouseUp = (e: MouseEvent) => {
            setHandleHeld(false);
        };

        if (handleHeld) {
            document.addEventListener('mousemove', handleMouseMove);
            document.addEventListener('mouseup', handleMouseUp);
        }

        return () => {
            document.removeEventListener('mousemove', handleMouseMove);
            document.removeEventListener('mouseup', handleMouseUp);
        };
    }, [widgetTop, widgetBottom, scrollableRef, contentsRef, prevMouseY, handleHeld]);

    const handleMouseDown = (e: React.MouseEvent) => {
        console.log('Mouse down');
        prevMouseY.current = e.clientY;
        setHandleHeld(true);
    };

    return (
        <div className="ScrollView" style={props.style}>
            <Rod className="ScrollView__Rod--left" widgetTop={`${widgetTop}px`} widgetBottom={`${widgetBottom}px`} />
            <Rod className="ScrollView__Rod--right" widgetTop={`${widgetTop}px`} widgetBottom={`${widgetBottom}px`} />
            <div className="ScrollView__contents" ref={scrollableRef}>
                <div className="ScrollView__contents-inner" ref={contentsRef}>
                    {props.children}
                </div>
            </div>
            <div className="ScrollView__handle-container" style={{ top: widgetTop, bottom: widgetBottom }}>
                <ScrollHandle
                    progress={progress}
                    barHeight={barHeight}
                    transitionDuration={0}
                    onMouseDown={handleMouseDown}
                />
                {/* <ScrollHandle progress={progress} barHeight={barHeight} transitionDuration={0.05} /> */}
                {/* <ScrollHandle progress={progress} barHeight={barHeight} transitionDuration={0.10} /> */}
            </div>
        </div>
    );
}