import { CircleGeometry, Color, DoubleSide, Mesh, MeshBasicMaterial, MeshLambertMaterial, MeshPhongMaterial, MeshPhysicalMaterial, Object3D, Sprite, SpriteMaterial, TextBufferGeometry, TextureLoader, Vector2 } from "three";
import {getFont} from "../../utils/loaders";
import Montserrat_Bold from "../../assets/fonts/Montserrat_Bold.json";
import Bender from "../../utils/bender";

const bender = new Bender()
interface IBubble {
    logoUrl?: string;
    linkUrl?: string;
    radius?: number; // m
    speed?: Vector2; // m/s
    acceleration?: Vector2; // m/s^2
    force?: Vector2; // m/s^2 * kg
    mass?: number; // kg
    position?: Vector2;
    color?: string | number | Color | undefined;
    text?:string;
}

export interface IBubbleCollisionInfo {
    bubble1: Bubble;
    bubble2: Bubble;
    dx: number;
    dy: number;
    d: number;
}

export interface IBubbleCollisionResult {
    collisionInfo?: IBubbleCollisionInfo,
    collided: boolean
}

export default class Bubble extends Object3D {
    logoUrl: string;
    linkUrl: string;
    color: string | number | Color | undefined;
    text: string;

    radius: number; // m
    speed: Vector2; // m/s
    acceleration: Vector2; // m/s^2
    force: Vector2; // m/s^2 * kg
    mass: number; // kg
    textMesh: Mesh|undefined;

    private material?: MeshBasicMaterial;
    private outlineMaterial?: MeshBasicMaterial;
    private geometry?: CircleGeometry;

    constructor(params: IBubble = {} as IBubble) {
        super();

        let {
            logoUrl = "",
            linkUrl = "",
            radius = 50,
            speed = new Vector2(0, 0),
            acceleration = new Vector2(0, 0),
            force = new Vector2(0, 0),
            mass = Math.PI * 2 * radius,
            position = new Vector2(0, 0),
            color = 0x292d2e,
            text = "",
        } = params;

        this.logoUrl = logoUrl;
        this.linkUrl = linkUrl;
        this.radius = radius;
        this.speed = speed;
        this.acceleration = acceleration;
        this.force = force;
        this.mass = mass;
        this.position.x = position.x;
        this.position.y = position.y;
        this.color = color;
        this.text = text;
        if(text != ""){
            this.radius = radius+10;
        }

        this.initShape();
    }

    private async initShape() {
        if(this.text == ""){
            this.geometry = new CircleGeometry(this.radius-3, 32);
        }else{
            this.geometry = new CircleGeometry(this.radius-10, 32);
        }
        this.material = new MeshBasicMaterial({ color: this.color, side: DoubleSide });
        this.outlineMaterial = new MeshBasicMaterial({ color: 0xf16f01, side: DoubleSide, transparent: true, opacity: 0 });

        var circle = new Mesh(this.geometry, this.material);
        this.add(circle);

        var outline = new Mesh(this.geometry, this.outlineMaterial);
        outline.scale.set(1.05, 1.05, 1.05);
        outline.position.z = -1;
        this.add(outline);

        var texture = new TextureLoader().load(this.logoUrl, (tex) => {

            var material2 = new SpriteMaterial({ map: texture });
            var sprite = new Sprite(material2);
            let x = 0, y = 0, factor = 0;
            let maxSize = this.radius-1 * Math.sqrt(2) * 0.95;

            if (tex.image.width > tex.image.height) {
                x = maxSize;
                factor = tex.image.width / x;
                y = tex.image.height / factor;
            } else {
                y = maxSize;
                factor = tex.image.height / y;
                x = tex.image.width / factor;
            }

            sprite.scale.set(x, y, 1);
            this.add(sprite);
        });
        if(this.text != ""){
            console.log(this.text)
            var font = await getFont(Montserrat_Bold)
            console.log("textloader")
            var textGeometry = new TextBufferGeometry( this.text, {
                size: 6.15,
                height: 1.5,
                curveSegments: 4,
                font: font
            });
            textGeometry.center()
            bender.bend(textGeometry, "y", -0.016)
            textGeometry.center()
            var textMaterial = new MeshPhysicalMaterial({
                color: 0x777777,
                emissive: 0xaa8800,
            });
            this.textMesh = new Mesh(textGeometry, textMaterial);

            // var direction = new Vector3(0, 0, -1);
            // var axis = new Vector3(0, 1, 0);
            // var angle = Math.PI / 6;

            // var modifier = new Bend();
            // modifier.set(direction, axis, angle).modify( text3D.geometry );
            this.textMesh.rotateY(-0.65);
            this.textMesh.rotateX(0.4);
            this.textMesh.position.z = this.radius/2;
            this.add(  this.textMesh );
            console.log(this)
        }
    }

    update(delta: number) {
        delta = delta < 1 / 20 ? delta : 1 / 20;
        this.calculateAcceleration();
        this.calculateMove(delta);
    }

    private calculateAcceleration() {
        this.acceleration.x = this.force.x / this.mass;
        this.acceleration.y = this.force.y / this.mass;
    }

    private calculateMove(delta: number) {
        this.speed.x += this.acceleration.x * delta;
        this.speed.y += this.acceleration.y * delta;
        this.position.x += this.speed.x * delta;
        this.position.y += this.speed.y * delta;
    }

    resetForce() {
        this.force.x = 0;
        this.force.y = 0;
    }
   
    rotateYText(x: number){
        this.textMesh?.rotateY(x);
    }

    changeScale(center_x: number, center_y: number, multi: number = 1){
        let dx = center_x - this.position.x;
        let dy = center_y - this.position.y;
        let d = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));

        let distance = 250;

        d = d/distance;

        let scale = d>1 ? 0.000001 : 1 - d*d;
        this.scale.setScalar(scale * multi);
    }

    checkCollision(bubble: Bubble): IBubbleCollisionResult {
        let dx = bubble.position.x - this.position.x;
        let dy = bubble.position.y - this.position.y;
        let d = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));
        if (d < this.radius + bubble.radius ) {
            let collisionInfo: IBubbleCollisionInfo = { bubble1: this, bubble2: bubble, dx, dy, d };
            return {
                collisionInfo: collisionInfo,
                collided: true
            }
        }
        return {
            collisionInfo: undefined,
            collided: false
        }
    }

    onClick() {
        if (this.linkUrl !== "")
            window.open(this.linkUrl, "_blank");
    }

    onMouseEnter() {
        this.outlineMaterial?.setValues({ opacity: 1 });
        this.position.z = 1;
    }

    onMouseLeave() {
        this.outlineMaterial?.setValues({ opacity: 0 });
        this.position.z = 0;
    }
}