// eslint-disable-next-line
import vertexShader from '!raw-loader!./particles.vert';
// eslint-disable-next-line
import fragmentShader from '!raw-loader!./particles.frag';

import { BufferGeometry, Float32BufferAttribute, Object3D, Points, ShaderMaterial, Vector2 } from "three";
import * as THREE from 'three';
import TouchTexture from './TouchTexture';
import { SmoothScalar } from '../../../utils/smoothScalar';
import { Easing } from '../../../utils/easing';

const glslify = require('glslify');

export interface ParticlesConfig {
    scale?: number
}

export default class Particles extends Object3D {

    angle = 0;
    PARICLE_COUNT = 200
    points!: Points;
    rotationCenters: number[] = [];
    touchTexture!: TouchTexture;
    width: number;
    height: number;
    options = {
        discardInvisible: false
    }
    opacity = new SmoothScalar(1, 3000, Easing.LINEAR);
    mouseX = 0;
    newMouseX = 0;
    uniforms = {
        uOpacity: { value: this.opacity.value },
        uTime: { value: 0 },
        uRandom: { value: 1.0 },
        uDepth: { value: 2.0 },
        uSize: { value: 0.0 },
        uScale: { value: 1.0 },
        uTouch: { value: null },
        uVisibleTexture: { value: null },
        uResolution: { value: new Vector2(0, 0) },
        uTextureSize: { value: new Vector2(64, 64) },
        uScroll: { value: 0 },
        uMouse: { value: new Vector2(0, 0) },
    };

    constructor(width: number, height: number, config: ParticlesConfig = {}) {
        super();
        this.width = width;
        this.height = height;
        this.setConfig(config);
        this.init()
    }

    private init() {
        this.touchTexture = new TouchTexture(this);
        this.initPoints();
    }

    private initPoints() {
        this.updateUResolution();

        var shaderMaterial = new THREE.ShaderMaterial({
            uniforms: this.uniforms,
            vertexShader: glslify(vertexShader),
            fragmentShader: glslify(fragmentShader),
            blending: THREE.NormalBlending,
            depthTest: true,
            depthWrite: false,
            transparent: true,
            vertexColors: true
        });
        shaderMaterial.uniforms.uTouch.value = this.touchTexture.texture;

        var geometry = new BufferGeometry();
        var vertices = [];
        var colors = [];
        var sizes = [];
        var indices = [];
        var angles = [];

        var color = new THREE.Color();

        for (var i = 0; i < this.PARICLE_COUNT; i++) {


            var x = Math.random() - 0.5;
            var y = Math.random() - 0.5;
            var z = Math.random() - 0.5;

            vertices.push(x, y, z);

            this.rotationCenters.push(x - 100);
            this.rotationCenters.push(y);
            this.rotationCenters.push(z);

            color.setHSL(i / this.PARICLE_COUNT, 1.0, 0.5);

            colors.push(color.r, color.g, color.b);

            sizes.push(1);

            indices.push(i);

            angles.push(Math.random() * Math.PI);
        }

        geometry.setAttribute('position', new Float32BufferAttribute(vertices, 3));
        geometry.setAttribute('color', new Float32BufferAttribute(colors, 3));
        geometry.setAttribute('size', new Float32BufferAttribute(sizes, 1).setUsage(THREE.DynamicDrawUsage));
        geometry.setAttribute('pindex', new Float32BufferAttribute(indices, 1));
        geometry.setAttribute('angle', new Float32BufferAttribute(angles, 1));

        this.points = new Points(geometry, shaderMaterial);

        this.add(this.points);
    }

    onUpdate(delta: number, elapsedTime: number, progress: number) {
        this.animate(delta, elapsedTime);
        this.touchTexture.update(0);
        (this.points.material as ShaderMaterial).uniforms.uTime.value = elapsedTime;
        (this.points.material as ShaderMaterial).uniforms.uScroll.value = progress;

        this.caluclateMouseX();

    }

    private setConfig(config: ParticlesConfig = {} as ParticlesConfig, material?:ShaderMaterial) {
        let uniformTarget = material ? material : this;

        let {
            scale
        } = config;

        if (scale){            
            this.scale.set(scale,scale,scale);
            uniformTarget.uniforms.uScale.value = scale;
        }

    }

    private animate(delta: number, elapsedTime: number) {
        this.rotation.y += 0.0003;
        this.rotation.x += 0.0005 * Math.sin(elapsedTime);
        this.opacity.update();
        (this.points.material as ShaderMaterial).uniforms.uOpacity.value = this.opacity.value;

    }

    private updateUResolution() {
        this.uniforms.uResolution = { value: new THREE.Vector2(this.width, this.height) };
    }

    onResize() {
        this.width = window.innerWidth;
        this.height = window.innerHeight;
        this.updateUResolution();
        (this.points.material as ShaderMaterial).uniforms.uResolution.value = this.uniforms.uResolution.value;
    }

    onMouseMove(e: MouseEvent) {
        this.touchTexture.addTouch({ x: e.clientX / this.width, y: e.clientY / this.height });
        this.newMouseX = e.clientX / this.width;
    }

    private caluclateMouseX() {
        let diff = this.newMouseX - this.mouseX;
        if (Math.abs(diff) > 0.001) {
            this.mouseX += Math.sign(diff) * 0.001 * (Math.pow(Math.abs(diff) + 1, 5));
            this.mouseX = Math.min(Math.max(this.mouseX, -1), 1);
        } else {
            this.mouseX = this.newMouseX;
        }
        (this.points.material as ShaderMaterial).uniforms.uMouse.value.x = this.mouseX;
    }


    fadeIn() {
        this.opacity.moveTo(1);
    }

    fadeOut() {
        this.opacity.moveTo(0);
    }

}
