import React, { useEffect, useRef, useState } from 'react';
import { useHasAppeared } from '../../hooks/useIsVisible';
import './Stat.scss';
import './StatContainer.scss';

export interface StatProps {
    name: string;
    value: number;
    icon?: string;
    unit?: string;
}

export function Stat(props: StatProps) {
    const ref = useRef<HTMLDivElement>(null);
    
    // Was visible at least once.
    const hasAppeared = useHasAppeared(ref);

    const [value, setValue] = useState(0);

    useEffect(() => {
        const fun = () => {
            let tmp = (1 - value * value) * 0.05 + 0.001;
            setValue(Math.min(value + tmp, 1));
        }

        if (hasAppeared && value < 1) {
            var timeout = setTimeout(fun, 30);
        }

        return () => {
            clearTimeout(timeout);
        };
    }, [ hasAppeared, value ]);

    return (
        <div className="Stat" ref={ref}>
            <div className={"Stat__icon " + props?.icon}></div>
            <div className="Stat__number">{Math.floor(value * props.value)} {props?.unit}</div>
            <div className="Stat__name">{props.name}</div>
        </div>
    );
}
